import React, { Component } from 'react';
import { Form, Row, Col, Button, Input, Icon } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { WrappedFormUtils } from 'antd/lib/form/Form';
import { Company } from './Company';
import { Bank } from './Bank';

export interface ContactFormProps extends FormComponentProps {
	fields: any;
	bankList: [string, string, string][];
	isOwn: boolean;
	isBankRequired: boolean;
	isBankExpanded: boolean;
	isContactExpanded: boolean;
	isSaving: boolean;
	onFieldsChange: (changedFields: any, props: ContactFormProps) => void;
	onValuesChange?: (values: any) => void;
	onSubmit: (e: React.FormEvent, form: WrappedFormUtils) => void;
	onToggleBankSection: () => void;
	onToggleContactSection: () => void;
}

const formLayout = {
	labelCol: {
		xs: { span: 24 },
		sm: { span: 8 },
		lg: { span: 8 }
	},
	wrapperCol: {
		xs: { span: 24 },
		sm: { span: 16 },
		lg: { span: 16 }
	}
};

const columnLayout = {
	xs: 24,
	sm: 16,
	lg: 12
};

class ContactForm extends Component<ContactFormProps> {
	handleSubmit = (e: React.FormEvent) => {
		this.props.onSubmit(e, this.props.form);
	};

	render() {
		const {
			form,
			isOwn,
			isBankRequired,
			isBankExpanded,
			isContactExpanded,
			isSaving,
			bankList,
			onToggleBankSection,
			onToggleContactSection
		} = this.props;
		const { getFieldDecorator } = form;

		return (
			<Form
				{...formLayout}
				layout="horizontal"
				className="fz-contact-form"
				onSubmit={this.handleSubmit}
			>
				{getFieldDecorator('id', {})(<Input type="hidden" />)}

				<Company {...form} isOwn={isOwn} />

				<Row className="fz-contact-row">
					<Col {...columnLayout}>
						<a
							className="fz-contact-row-expander"
							onClick={onToggleBankSection}
						>
							Bankov spojenie <Icon type={isBankExpanded ? 'up' : 'down'} />
						</a>
					</Col>
				</Row>

				<div
					className={`fz-contact-row-collapsible fz-contact-row--${
						isOwn || isBankExpanded ? 'expanded' : 'collapsed'
					}`}
				>
					<h4 className="fz-contact-header fz-contact-header--expanded">
						Bankov spojenie
					</h4>
					<Bank {...form} isRequired={isBankRequired} bankList={bankList} />
				</div>

				<Row className="fz-contact-row">
					<Col {...columnLayout}>
						<a
							className="fz-contact-row-expander"
							onClick={onToggleContactSection}
						>
							Kontaktn osoba <Icon type={isContactExpanded ? 'up' : 'down'} />
						</a>
					</Col>
				</Row>

				<div
					className={`fz-contact-row-collapsible fz-contact-row--${
						isContactExpanded ? 'expanded' : 'collapsed'
					}`}
				>
					<h4 className="fz-contact-header fz-contact-header--expanded">
						Kontaktn osoba
					</h4>
					<Row>
						<div className="fz-contact-row fz-contact-row">
							<Col {...columnLayout}>
								<Form.Item label="Meno">
									{getFieldDecorator('name', {
										preserve: true,
										rules: [
											{
												required: false,
												message: 'Uvete meno!'
											}
										]
									})(<Input />)}
								</Form.Item>
								<Form.Item label="Email">
									{getFieldDecorator('email', {
										preserve: true,
										rules: [
											{
												type: 'email',
												message: 'Zadan text nie je platn email!'
											}
										]
									})(<Input />)}
								</Form.Item>
								<Form.Item label="Tel. slo">
									{getFieldDecorator('phoneNr', {
										preserve: true,
										rules: [
											{
												required: false,
												message: 'Uvete tel. slo!'
											}
										]
									})(<Input />)}
								</Form.Item>
							</Col>
						</div>
					</Row>
				</div>

				<Row className="fz-contact-row">
					<Col span={24} style={{ textAlign: 'right', margin: 15 }}>
						<div className="fz-btn-group">
							<Button
								type="primary"
								icon="save"
								size="large"
								htmlType="submit"
								loading={isSaving}
							>
								Uloi
							</Button>
						</div>
					</Col>
				</Row>
			</Form>
		);
	}
}

export const EnhancedContactForm = Form.create({
	name: 'contact',
	onFieldsChange(props: ContactFormProps, changedFields, allFields: any) {
		props.onFieldsChange(changedFields, allFields);
	},
	mapPropsToFields(props: ContactFormProps) {
		const { fields, bankList, isOwn } = props;

		if (Object.keys(fields).length === 0) {
			return {
				isOwn: Form.createFormField({ value: isOwn })
			};
		}

		return {
			id: Form.createFormField(fields.id),
			entity: Form.createFormField(fields.entity),
			street: Form.createFormField(fields.street),
			zipCode: Form.createFormField(fields.zipCode),
			city: Form.createFormField(fields.city),
			country: Form.createFormField(fields.country),
			legalId: Form.createFormField(fields.legalId),
			taxId: Form.createFormField(fields.taxId),
			taxLegalId: Form.createFormField(fields.taxLegalId),
			bankIndex: Form.createFormField({
				value: bankList.find(
					([bankName, bankCode, swift]: any) =>
						bankCode === fields.bankCode.value
				)
			}),
			bankName: Form.createFormField(fields.bankName),
			bankCode: Form.createFormField(fields.bankCode),
			swift: Form.createFormField(fields.swift),
			iban: Form.createFormField(fields.iban),
			bankAccount: Form.createFormField(fields.bankAccount),
			name: Form.createFormField(fields.name),
			email: Form.createFormField(fields.email),
			phoneNr: Form.createFormField(fields.phoneNr),
			isOwn: Form.createFormField(isOwn ? { value: isOwn } : fields.isOwn)
		};
	},
	onValuesChange(props: ContactFormProps, values) {
		props.onValuesChange && props.onValuesChange(values);
	}
})(ContactForm);
