import { History } from 'history';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { VisibilityFilters, createInvoice, deleteInvoices } from '../actions';
import { InvoiceList } from '../components/InvoiceList';
import { Invoice } from '../classes/Invoice';

const getVisibleInvoices = (invoices: any, filter: any) => {
	switch (filter) {
		case VisibilityFilters.SHOW_ALL:
			return invoices;
		case VisibilityFilters.SHOW_COMPLETED:
			return invoices.filter((i: any) => i.completed);
		case VisibilityFilters.SHOW_ACTIVE:
			return invoices.filter((i: any) => !i.completed);
		default:
			return invoices;
	}
};

const mapStateToProps = (state: any) => {
	const invoices: Invoice[] = getVisibleInvoices(
		state.invoices,
		state.visibilityFilter
	);
	return {
		invoices: invoices
			.map(invoice => new Invoice(invoice))
			.sort((a, b) => b.issueDate.getTime() - a.issueDate.getTime()),
		ui: state.ui
	};
};

const mapDispatchToProps = (dispatch: any) => {
	return {
		onCreate: (routerHistory: History) => {
			dispatch(createInvoice(routerHistory));
		},
		onDelete: (keys: string[]) => {
			dispatch(deleteInvoices(keys));
		}
	};
};

export const VisibleInvoiceList = withRouter(connect(
	mapStateToProps,
	mapDispatchToProps
)(InvoiceList) as any);
