import React, { Component } from 'react';
import { History, Location } from 'history';
import { PageHeader, message } from 'antd';
import { WrappedFormUtils } from 'antd/lib/form/Form';
import LoadingOverlay from 'react-loading-overlay';
import { Spinner } from '../Spinner';
import { EnhancedContactForm as ContactForm } from './ContactForm';
import { Contact } from '../../classes/Contact';
import { ContactUIState } from '../../reducers/contactUI';
import './style.css';

export interface EditContactProps extends ContactUIState {
	contactId: string;
	forceOwn: boolean;
	isBankRequired: boolean;
	history: History;
	location: Location;
	bankList: [string, string, string][];
	fetchForm: (id: string) => void;
	onSave: (values: any, routerHistory: History) => Promise<Contact>;
	onFieldsChange: (changedFields: any, allFields: any) => void;
	onToggleBankSection: () => void;
	onToggleContactSection: () => void;
}

export class EditContact extends Component<EditContactProps> {
	componentWillMount() {
		this.props.fetchForm(this.props.contactId);
	}

	componentWillReceiveProps(nextProps: EditContactProps) {
		if (nextProps.contactId !== this.props.contactId) {
			this.props.fetchForm(nextProps.contactId);
		}
	}

	handleSubmit = (e: React.FormEvent, form: WrappedFormUtils) => {
		e.preventDefault();
		form.validateFieldsAndScroll((err: Error, values: any) => {
			if (err) {
				message.error('Formulr obsahuje chyby!');
				return false;
			}
			console.log('submit', JSON.stringify(values));
			this.props.onSave(values, this.props.history).catch(error => {
				message.error(error.message);
			});
		});
	};

	render() {
		const {
			contactId,
			forceOwn,
			isSaving,
			isFetching,
			isBankRequired,
			isBankExpanded,
			isContactExpanded,
			fields,
			bankList,
			onFieldsChange,
			onToggleBankSection,
			onToggleContactSection
		} = this.props;

		return (
			<div>
				<LoadingOverlay
					classNamePrefix="fz-loading-overlay_"
					active={isFetching}
					spinner={<Spinner />}
					text="Nahrvam kontakt..."
					styles={{
						overlay: {},
						content: {}
					}}
				>
					<PageHeader
						onBack={() => this.props.history.goBack()}
						title={`Kontakt #${contactId}`}
						subTitle=""
					/>
					<ContactForm
						fields={fields}
						bankList={bankList}
						isOwn={forceOwn}
						isBankRequired={isBankRequired}
						isSaving={isSaving}
						isBankExpanded={isBankExpanded}
						isContactExpanded={isContactExpanded}
						onFieldsChange={onFieldsChange}
						onSubmit={this.handleSubmit}
						onToggleBankSection={onToggleBankSection}
						onToggleContactSection={onToggleContactSection}
					/>
				</LoadingOverlay>
			</div>
		);
	}
}
