import React, { Component } from 'react';
import { Form, Icon, Input, Button, Checkbox } from 'antd';
import './style.css';

class NormalLoginForm extends Component<any, any> {
	handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		this.props.form.validateFields((err: Error, values: any) => {
			if (!err) {
				this.props.onLogIn(values.email, values.password, this.props.history);
			}
		});
	};

	render() {
		const { getFieldDecorator } = this.props.form;
		return (
			<Form onSubmit={this.handleSubmit} className="fz-login-form">
				<Form.Item>
					{getFieldDecorator('email', {
						rules: [{ required: true, message: 'Please input your email!' }]
					})(
						<Input
							prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
							placeholder="Email"
						/>
					)}
				</Form.Item>
				<Form.Item>
					{getFieldDecorator('password', {
						rules: [{ required: true, message: 'Please input your Password!' }]
					})(
						<Input
							prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
							type="password"
							placeholder="Password"
						/>
					)}
				</Form.Item>
				<Form.Item>
					{getFieldDecorator('remember', {
						valuePropName: 'checked',
						initialValue: true
					})(<Checkbox>Remember me</Checkbox>)}
					<a className="fz-login-form-forgot" href="#/forgot-passwd">
						Forgot password
					</a>
					<Button
						type="primary"
						htmlType="submit"
						className="fz-login-form-button"
					>
						Log in
					</Button>
					Or <a href="#/register">register now!</a>
				</Form.Item>
			</Form>
		);
	}
}

const WrappedNormalLoginForm = Form.create<any>({ name: 'normal_login' })(
	NormalLoginForm
);

export { WrappedNormalLoginForm as LoginForm };
