import { types as t, Action } from '../actions';
import { Contact } from '../classes/Contact';
import { InvoiceType } from '../classes/Invoice';

export type InvoiceUIState = {
	fields: any;
	itemKeys: number[];
	isDraft: boolean;
	isFetching: boolean;
	isSaving: boolean;
};

const initialState = {
	fields: {} as any,
	itemKeys: [] as number[],
	isDraft: false,
	isFetching: false,
	isSaving: false
};

const ui = (
	state: InvoiceUIState = initialState,
	action: Action
): InvoiceUIState => {
	switch (action.type) {
		case t.INVOICE_FETCH:
			return { ...state, isFetching: true };
		case t.INVOICE_FETCH_SUCCESS:
			return { ...state, isFetching: false };
		case t.INVOICE_FETCH_FAILURE:
			return { ...state, isFetching: false };
		case t.INVOICE_SAVE:
			return { ...state, isSaving: true, isDraft: action.payload.isDraft };
		case t.INVOICE_SAVE_SUCCESS:
			return { ...state, isSaving: false };
		case t.INVOICE_SAVE_FAILURE:
			return { ...state, isSaving: false };
		case t.INVOICE_FORM_REPLACE: {
			return { ...state, ...action.payload };
		}
		case t.CONTACT_SAVE_SUCCESS: {
			const { fields } = state;
			const contact: Contact = action.payload;
			if (contact.isOwn) {
				const type = contact.isTaxable
					? InvoiceType.Taxable
					: InvoiceType.NonTax;
				fields.type = { value: type };
				fields.emitent = { value: contact.id };
			} else {
				fields.recipient = { value: contact.id };
			}
			return { ...state, fields };
		}
		default:
			return state;
	}
};

export const invoiceUI = (
	state: InvoiceUIState = initialState,
	action: Action
): InvoiceUIState => ui(state, action);
