import { History } from 'history';
import { Action } from 'redux';
import { ThunkAction } from 'redux-thunk';
import * as t from './actionTypes';
import client from '../api';
import { AppState } from '../reducers';
import { createContact } from './contact';
import { Contact } from '../classes/Contact';
import { Invoice, InvoiceType } from '../classes/Invoice';
import { InvoiceMapper } from '../classes/InvoiceMapper';
import { NotFoundException } from '../classes/NotFoundException';
import { padLeft } from '../utils/padLeft';

export type InvoiceAction<R> = ThunkAction<R, AppState, null, Action<string>>;

const keysInRange = (length: number) =>
	Array.from({ length: Math.max(0, length) }, (v, i) => i);

export const createInvoice = (routerHistory: History): InvoiceAction<void> => (
	dispatch,
	getState
) => {
	const { invoices } = getState();
	const year = new Date().getFullYear();
	const yearStr = String(year);
	const idTuple = invoices.reduce(
		([lastId, count], invoice: Invoice) => {
			const { id } = invoice;
			if (id && id.startsWith(yearStr)) {
				count++;
				const invoiceId = parseInt(id.slice(yearStr.length), 10);
				if (invoiceId > lastId) {
					lastId = invoiceId;
				}
			}
			return [lastId, count];
		},
		[0, 0]
	);
	routerHistory.push(
		`/invoices/${year}${padLeft(Math.max(...idTuple) + 1, 3)}`
	);
};

export const fetchInvoices = (): InvoiceAction<
	Promise<Invoice[]>
> => dispatch => {
	dispatch({ type: t.INVOICES_FETCH, payload: {} });
	return client.fetchInvoices().then(
		invoices => {
			dispatch({
				type: t.INVOICES_FETCH_SUCCESS,
				payload: invoices
			});
			return invoices;
		},
		error => {
			dispatch({
				type: t.INVOICES_FETCH_FAILURE,
				payload: error
			});
			// Rethrow so returned Promise is rejected
			throw error;
		}
	);
};

export const fetchInvoice = (
	id: string
): InvoiceAction<Promise<Invoice>> => dispatch => {
	dispatch({ type: t.INVOICE_FETCH, payload: {} });
	return client
		.fetchInvoice(id)
		.then(invoice => {
			if (!invoice) {
				throw new NotFoundException(`Faktra \`${id}\` sa nenala!`);
			}
			dispatch({
				type: t.INVOICE_FETCH_SUCCESS,
				payload: invoice
			});
			return invoice;
		})
		.catch(error => {
			dispatch({
				type: t.INVOICE_FETCH_FAILURE,
				payload: error
			});
			// Rethrow so returned Promise is rejected
			throw error;
		});
};

export const updateInvoiceForm = (
	changedFields: any,
	allFields: any,
	itemKeys?: number[]
): InvoiceAction<void> => (dispatch, getState) => {
	const payload: any = { fields: allFields };
	if (changedFields.emitent) {
		const { contacts } = getState();
		const emitent = contacts.find(
			contact => changedFields.emitent.value === contact.id
		);
		if (emitent) {
			payload.fields.type = {
				value: new Contact(emitent).isTaxable
					? InvoiceType.Taxable
					: InvoiceType.NonTax
			};
		}
	}
	if (itemKeys) {
		payload.itemKeys = itemKeys;
	}
	dispatch({
		type: t.INVOICE_FORM_REPLACE,
		payload
	});
};

export const fetchInvoiceForm = (id: string): InvoiceAction<void> => (
	dispatch,
	getState
) => {
	const { invoices, invoiceUI } = getState();
	const { fields, isDraft } = invoiceUI;
	if (isDraft && fields.id && fields.id.value === id) {
		console.log('Skip form fetch');
		return void 0;
	}

	Promise.resolve(invoices.find(invoice => invoice.id === id))
		.then(invoice => {
			if (invoice) {
				return invoice;
			}
			return dispatch(fetchInvoice(id)).catch(error => {
				if (error instanceof NotFoundException) {
					return { id };
				}
				throw error;
			});
		})
		.then(invoice => {
			invoice = new Invoice(invoice);
			const fields = new InvoiceMapper().toFields(invoice);
			const itemKeys = keysInRange(Math.max(1, invoice.items.length));
			return dispatch(updateInvoiceForm({}, fields, itemKeys));
		});
};

export const checkInvoiceExists = (
	id: string
): InvoiceAction<Promise<boolean>> => (dispatch, getState) => {
	const { invoices } = getState();
	const invoice = invoices.find(invoice => invoice.id === id);
	return Promise.resolve(!!invoice);
};

export const addItem = (invoiceId: string): InvoiceAction<void> => (
	dispatch,
	getState
) => {
	const { invoiceUI } = getState();
	let { itemKeys } = invoiceUI;

	itemKeys = [
		...itemKeys,
		itemKeys.length === 0 ? 0 : itemKeys[itemKeys.length - 1] + 1
	];

	dispatch({
		type: t.INVOICE_FORM_REPLACE,
		payload: { itemKeys }
	});
};

export const removeItem = (
	invoiceId: string,
	k: number
): InvoiceAction<void> => (dispatch, getState) => {
	const { invoiceUI } = getState();
	let { itemKeys, fields } = invoiceUI;

	// We need at least one item
	if (itemKeys.length === 1) {
		return false;
	}

	[
		'itemsNames',
		'pricesPU',
		'units',
		'amounts',
		'taxRates',
		'itemKeys'
	].forEach(prop => {
		if (fields.hasOwnProperty(prop)) {
			delete fields[prop][k];
		}
	});

	itemKeys = itemKeys.filter((key: any) => key !== k);

	dispatch({
		type: t.INVOICE_FORM_REPLACE,
		payload: {
			itemKeys,
			fields: { ...fields }
		}
	});
};

const mapInvoice = (values: any, itemKeys: number[], state: AppState) => {
	const { contacts, invoices } = state;
	let invoice = new InvoiceMapper().fromValues(values, itemKeys);
	let emitent = contacts.find(contact => values.emitent === contact.id);
	let recipient = contacts.find(contact => values.recipient === contact.id);
	if (!(emitent && recipient)) {
		const origInvoice = invoices.find(inv => inv.id === invoice.id);
		if (origInvoice) {
			emitent = emitent || origInvoice.emitent;
			recipient = recipient || origInvoice.recipient;
		}
	}
	return new Invoice({ ...invoice, emitent, recipient });
};

export const saveInvoice = (
	values: any,
	itemKeys: number[],
	isDraft: boolean = false
): InvoiceAction<Promise<Invoice>> => (dispatch, getState) => {
	const invoice = mapInvoice(values, itemKeys, getState());
	dispatch({ type: t.INVOICE_SAVE, payload: { isDraft } });
	return client.saveInvoice(invoice).then(
		invoice => {
			invoice = new Invoice(invoice);
			dispatch({
				type: t.INVOICE_SAVE_SUCCESS,
				payload: invoice
			});
			return invoice;
		},
		error => {
			dispatch({
				type: t.INVOICE_SAVE_FAILURE,
				payload: error
			});
			// Rethrow so returned Promise is rejected
			throw error;
		}
	);
};

export const deleteInvoices = (
	keys: string[]
): InvoiceAction<void> => dispatch => {
	dispatch({ type: t.INVOICES_DELETE, payload: keys });
	return client.deleteInvoices(keys).then(
		invoices => {
			dispatch({
				type: t.INVOICES_DELETE_SUCCESS,
				payload: invoices
			});
		},
		error => {
			dispatch({
				type: t.INVOICES_DELETE_FAILURE,
				payload: error
			});
			// Rethrow so returned Promise is rejected
			throw error;
		}
	);
};

export const changeInvoiceId = (
	newId: string,
	values: any,
	itemKeys: number[],
	routerHistory: History
): InvoiceAction<void> => (dispatch, getState) => {
	const invoice = mapInvoice(values, itemKeys, getState());
	const oldId = invoice.id as string;
	invoice.id = newId;
	dispatch({ type: t.INVOICE_SAVE, payload: {} });
	return client
		.saveInvoice(invoice)
		.then(invoice => {
			routerHistory.replace(`/invoices/${invoice.id}`); // import if invoice id has changed
			if (oldId !== invoice.id) {
				dispatch(deleteInvoices([oldId]));
			}
			return new Invoice(invoice);
		})
		.then(invoice => {
			dispatch({
				type: t.INVOICE_SAVE_SUCCESS,
				payload: invoice
			});
		})
		.catch((error: Error) => {
			dispatch({
				type: t.INVOICE_SAVE_FAILURE,
				payload: error
			});
			// Rethrow so returned Promise is rejected
			throw error;
		});
};

export const createEntity = (
	values: any,
	itemKeys: number[],
	isOwn: boolean,
	routerHistory: History
): InvoiceAction<void> => dispatch => {
	return dispatch(saveInvoice(values, itemKeys, true)).then(() => {
		return dispatch(createContact(routerHistory, isOwn));
	});
};
