import React from 'react';
import ReactDOM from 'react-dom';
import {
	HashRouter as Router,
	Switch,
	Redirect,
	Route
} from 'react-router-dom';
import { createStore, applyMiddleware, compose, AnyAction } from 'redux';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';
import { LocaleProvider, notification } from 'antd';
import skSK from 'antd/lib/locale-provider/sk_SK';
import App, { routes } from './App';
import { NoMatch } from './components/NoMatch';
import { Login } from './containers/Login';
import { PrivateRoute } from './containers/PrivateRoute';
import { fetchContacts, fetchInvoices } from './actions';
import invoiceApp from './reducers';
import bankList from './api/bankList';
import * as serviceWorker from './serviceWorker';
import 'moment/locale/sk';
import './index.css';

const composeEnhancers =
	(window as any).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(
	invoiceApp,
	{ bankList },
	composeEnhancers(applyMiddleware(thunk))
);

store.dispatch((fetchContacts() as unknown) as AnyAction);
store.dispatch((fetchInvoices() as unknown) as AnyAction);

ReactDOM.render(
	<Provider store={store}>
		<LocaleProvider locale={skSK}>
			<Router>
				<Switch>
					<Redirect exact from="/" to="/invoices" />
					<Route exact path="/login" component={Login} />
					{routes.map(({ path }, i) => (
						<PrivateRoute key={i} path={path} component={App} />
					))}
					<Route component={NoMatch} />
				</Switch>
			</Router>
		</LocaleProvider>
	</Provider>,
	document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
