import { History } from 'history';
import * as t from './actionTypes';
import client from '../api';
import { fetchContacts } from './contact';

export const logIn = (
	email: string,
	password: string,
	routerHistory: History
) => (dispatch: any) => {
	dispatch({ type: t.LOG_IN, payload: {} });
	return client
		.doLogin(email, password)
		.then(() => {
			dispatch({
				type: t.LOG_IN_SUCCESS,
				payload: {}
			});
			fetchContacts();
		})
		.catch(error => {
			dispatch({
				type: t.LOG_IN_FAILURE,
				payload: error
			});
			// Rethrow so returned Promise is rejected
			throw error;
		});
};

export const logOut = (routerHistory: any) => ({
	type: t.LOG_OUT,
	payload: {}
});
