export const delayPromise = (ms: number) => {
	return new Promise(resolve => setTimeout(resolve, ms));
};

export const hasMethod = (obj: any, name: string) => {
	const desc = Object.getOwnPropertyDescriptor(obj, name);
	return !!desc && typeof desc.value === 'function';
};

export const getInstanceMethodNames = (obj: any, stop?: any) => {
	let array: any[] = [];
	let proto = Object.getPrototypeOf(obj);
	while (proto && proto !== stop) {
		Object.getOwnPropertyNames(proto).forEach(name => {
			if (name !== 'constructor') {
				if (hasMethod(proto, name)) {
					array.push(name);
				}
			}
		});
		proto = Object.getPrototypeOf(proto);
	}
	return array;
};

export class DelayProxyBuilder<T> {
	private client: any;
	private delay: number = 0;
	private _skipMethods: (string | RegExp)[] = [];

	setClient(client: any) {
		this.client = client;
		return this;
	}

	setDelay(delayInMillis: number) {
		this.delay = delayInMillis;
		return this;
	}

	skipMethods(skipMethods: (string | RegExp)[]) {
		this._skipMethods = skipMethods;
		return this;
	}

	build(): T & DelayProxy<T> {
		/* eslint-disable-next-line @typescript-eslint/no-use-before-define */
		const proxy = new DelayProxy<T>(this.client, this.delay, this._skipMethods);
		return (proxy as unknown) as T & DelayProxy<T>;
	}
}

export class DelayProxy<T> {
	constructor(
		private readonly client: any,
		private readonly delayInMillis: number = 1000,
		private readonly skipMethods: (string | RegExp)[] = []
	) {
		const proxy = this as any;
		const methods = getInstanceMethodNames(client, Object.prototype);
		methods.forEach(method => {
			const skip = skipMethods.some(skipMethod => {
				if (skipMethod instanceof RegExp) {
					return skipMethod.test(method);
				}
				return method === skipMethod;
			});
			if (skip) {
				return;
			}
			proxy[method] = (...args: any) => {
				return delayPromise(delayInMillis).then(() => client[method](...args));
			};
		});
	}

	setDelay(delayInMillis: number): T & DelayProxy<T> {
		const { client } = this;
		return new DelayProxyBuilder<typeof client>()
			.setClient(client)
			.setDelay(delayInMillis)
			.skipMethods(this.skipMethods)
			.build();
	}
}
