import moment from 'moment';
import { Invoice, PaymentTypeEnum } from './Invoice';
import { InvoiceItem } from './InvoiceItem';
import {
	dateFormat,
	currencyFormat,
	formatIBAN,
	formatZipCode
} from '../utils/formatters';
import { Contact } from './Contact';

const DEFAULT_INVOICE_DESCRIPTION =
	'Fakturujeme Vm za dodan tovar alebo sluby:';
const DEFAULT_INVOICE_LAST_TEXT =
	'Tovar zostva a do plnho uhradenia majetkom dodvatea. Pri oneskorenej hrade Vm budeme tova penle vo vke 0,05% za kad zaat de omekania.';

const formatDate = (date?: Date) => {
	if (date) {
		const dateAsString = dateFormat.format(date);
		return dateAsString.toUpperCase().replace(/[^A-Z0-9\\.]/g, '');
	}
};

export class InvoiceMapper {
	toItemFields(items: InvoiceItem[]) {
		return items.reduce(
			(fields, item, i) => {
				fields.itemsNames[i] = { value: item.name };
				fields.pricesPU[i] = { value: item.pricePerUnit };
				fields.units[i] = { value: item.unit };
				fields.amounts[i] = { value: item.amount };
				fields.taxRates[i] = { value: item.taxRate };
				return fields;
			},
			{
				itemsNames: [],
				pricesPU: [],
				units: [],
				amounts: [],
				taxRates: []
			} as any
		);
	}

	toFields(invoice: Invoice) {
		const { recipient, emitent, items } = invoice;

		return {
			id: {
				value: invoice.id
			},
			type: {
				value: invoice.type
			},
			recipient: {
				value: (recipient && recipient.entity) || null
			},
			emitent: {
				value: (emitent && emitent.entity) || null
			},
			paymentType: {
				value: invoice.paymentType
			},
			dueDate: {
				value: invoice.dueDate && moment(invoice.dueDate)
			},
			issueDate: {
				value: invoice.issueDate && moment(invoice.issueDate)
			},
			deliveryDate: {
				value: invoice.deliveryDate && moment(invoice.deliveryDate)
			},
			paymentDate: {
				value: invoice.paymentDate && moment(invoice.paymentDate)
			},
			variableSymbol: {
				value: invoice.variableSymbol || invoice.id
			},
			constantSymbol: {
				value: invoice.constantSymbol
			},
			orderNr: {
				value: invoice.orderNr
			},
			description: {
				value: invoice.description || DEFAULT_INVOICE_DESCRIPTION
			},
			finalText: {
				value: invoice.finalText || DEFAULT_INVOICE_LAST_TEXT
			},
			discount: {
				value: invoice.discount
			},
			...this.toItemFields(items)
		};
	}

	toInvoiceItems(fields: any, itemKeys: number[]) {
		const {
			itemsNames = [],
			pricesPU = [],
			units = [],
			amounts = [],
			taxRates = []
		} = fields;
		const items: InvoiceItem[] = [];
		itemKeys.forEach(key => {
			items[key] = new InvoiceItem({
				name: itemsNames[key] && itemsNames[key].value,
				unit: units[key] && units[key].value,
				pricePerUnit: pricesPU[key] && pricesPU[key].value,
				amount: amounts[key] && amounts[key].value,
				taxRate: taxRates[key] && taxRates[key].value
			});
		});
		return items;
	}

	fromFields(fields: any, itemKeys: number[]) {
		const {
			id = {},
			type = {},
			paymentType = {},
			dueDate = {},
			issueDate = {},
			deliveryDate = {},
			paymentDate = {},
			variableSymbol = {},
			constantSymbol = {},
			orderNr = {},
			description = {},
			finalText = {},
			discount = {}
		} = fields;
		const invoice = new Invoice({
			id: id.value,
			type: Number(type.value),
			paymentType: paymentType.value,
			dueDate: dueDate.value && dueDate.value.toDate(),
			issueDate: issueDate.value && issueDate.value.toDate(),
			deliveryDate: deliveryDate.value && deliveryDate.value.toDate(),
			paymentDate: paymentDate.value && paymentDate.value.toDate(),
			variableSymbol: variableSymbol.value,
			constantSymbol: constantSymbol.value,
			orderNr: orderNr.value,
			description: description.value,
			finalText: finalText.value,
			discount: discount.value,
			items: this.toInvoiceItems(fields, itemKeys)
		});
		return invoice;
	}

	fromValues(values: any, itemKeys: number[]) {
		const { itemsNames, pricesPU, units, amounts, taxRates } = values;
		const items: InvoiceItem[] = itemKeys.map(key => {
			return new InvoiceItem({
				name: itemsNames[key],
				unit: units[key],
				pricePerUnit: pricesPU[key],
				amount: amounts[key],
				taxRate: (taxRates && taxRates[key]) || 0
			});
		});

		const invoice = new Invoice({
			id: values.id,
			type: Number(values.type),
			paymentType: values.paymentType,
			dueDate: values.dueDate && values.dueDate.toDate(),
			issueDate: values.issueDate && values.issueDate.toDate(),
			deliveryDate: values.deliveryDate && values.deliveryDate.toDate(),
			paymentDate: values.paymentDate && values.paymentDate.toDate(),
			variableSymbol: values.variableSymbol,
			constantSymbol: values.constantSymbol,
			orderNr: values.orderNr,
			description: values.description,
			finalText: values.finalText,
			discount: values.discount,
			items: items
		});
		return invoice;
	}

	toPlainObject(invoice: Invoice) {
		const formatCurrency = currencyFormat.format;
		const { emitent = new Contact(), recipient = new Contact() } = invoice;

		let paymentType = '';
		switch (invoice.paymentType) {
			case PaymentTypeEnum.WireTransfer:
				paymentType = 'Prevodom';
				break;
			case PaymentTypeEnum.Card:
				paymentType = 'Kartou';
				break;
			case PaymentTypeEnum.Cash:
				paymentType = 'V hotovosti';
				break;
			case PaymentTypeEnum.CashOnDelivery:
				paymentType = 'Dobierka';
				break;
			default:
			/* noop */
		}

		return {
			id: invoice.id,
			type: invoice.type,
			paymentType: paymentType,
			dueDate: invoice.dueDate && formatDate(invoice.dueDate),
			issueDate: invoice.issueDate && formatDate(invoice.issueDate),
			deliveryDate: invoice.deliveryDate && formatDate(invoice.deliveryDate),
			paymentDate: invoice.paymentDate && formatDate(invoice.paymentDate),
			variableSymbol: invoice.variableSymbol,
			constantSymbol: invoice.constantSymbol,
			orderNr: invoice.orderNr,
			description: invoice.description,
			finalText: invoice.finalText,
			discount: invoice.discount,
			items: invoice.items.map(item => ({
				...item,
				pricePerUnit: formatCurrency(item.pricePerUnit),
				grossPrice: formatCurrency(item.grossPrice),
				netPrice: formatCurrency(item.netPrice),
				taxPrice: formatCurrency(item.taxPrice)
			})),
			emitent: {
				...invoice.emitent,
				iban: formatIBAN(emitent.iban as string),
				zipCode: formatZipCode(emitent.zipCode as string)
			},
			recipient: {
				...invoice.recipient,
				zipCode: formatZipCode(recipient.zipCode as string)
			},
			grossPrice: formatCurrency(invoice.grossPrice),
			netPrice: formatCurrency(invoice.netPrice),
			discountPrice: formatCurrency(invoice.discountPrice)
		};
	}
}
