import React, { Component } from 'react';
import { Form, Input } from 'antd';
import { FormComponentProps } from 'antd/lib/form/Form';
import { debounce } from '../../utils/debounce';

export interface InvoiceIdFormProps extends FormComponentProps {
	invoiceId?: string;
	onInvoiceCheckExists: (value: any) => Promise<boolean>;
}

class InvoiceIdForm extends Component<InvoiceIdFormProps> {
	constructor(props: InvoiceIdFormProps) {
		super(props);
		this.assertUniqueId = debounce(this.assertUniqueId, 250, false);
	}

	assertUniqueId = (rule: any, value: any, callback: any) => {
		if (!value) {
			return callback();
		}
		this.props.onInvoiceCheckExists(value).then(doesExists => {
			callback(doesExists ? true : undefined);
		});
	};

	render() {
		const { getFieldDecorator } = this.props.form;
		const { invoiceId } = this.props;

		return (
			<Form>
				<Form.Item hasFeedback label="Nov identifiktor">
					{getFieldDecorator('invoiceId', {
						rules: [
							{
								required: true,
								message: 'Vyplte id dokladu!'
							},
							{
								validator: this.assertUniqueId,
								message: 'Id dokladu u bolo pouit!'
							}
						],
						initialValue: invoiceId
					})(<Input />)}
				</Form.Item>
			</Form>
		);
	}
}

export const EnhancedInvoiceIdForm = Form.create<InvoiceIdFormProps>({
	name: 'change_invoice_id'
})(InvoiceIdForm);
