import { History } from 'history';
import queryString from 'query-string';
import { withRouter } from 'react-router-dom';
import { AnyAction } from 'redux';
import { ThunkDispatch } from 'redux-thunk';
import { connect } from 'react-redux';
import { EditContact as EditContactComp } from '../components/EditContact';
import {
	saveContact,
	fetchContactForm,
	updateContactForm,
	types as t
} from '../actions';
import { AppState } from '../reducers';

const selectFields = (state: AppState, ownProps: any) => {
	const { contactUI } = state;
	const { match } = ownProps;
	const { id } = match.params;

	if (contactUI.fields.id && contactUI.fields.id.value === id) {
		return contactUI.fields;
	}

	return {};
};

const mapStateToProps = (state: AppState, ownProps: any) => {
	const { contactUI, bankList } = state;
	const { location, match } = ownProps;
	const { id } = match.params;
	const qs = queryString.parse(location.search);
	const forceOwn = qs.isOwn === 'true';
	const fields = selectFields(state, ownProps);

	return {
		...contactUI,
		contactId: id,
		fields,
		bankList,
		forceOwn,
		isBankExpanded:
			forceOwn ||
			(fields.isOwn && (fields.isOwn.value || contactUI.isBankExpanded)),
		isBankRequired: forceOwn || (fields.isOwn && fields.isOwn.value)
	};
};

const mapDispatchToProps = (
	dispatch: ThunkDispatch<AppState, null, AnyAction>
) => ({
	fetchForm: (id: string) => dispatch(fetchContactForm(id)),
	onSave: (values: any, routerHistory: History) =>
		dispatch(saveContact(values, routerHistory)),
	onFieldsChange: (changedFields: any, allFields: any) =>
		dispatch(updateContactForm(changedFields, allFields)),
	onToggleBankSection: () =>
		dispatch({ type: t.CONTACT_FORM_TOGGLE, payload: 'bank' }),
	onToggleContactSection: () =>
		dispatch({ type: t.CONTACT_FORM_TOGGLE, payload: 'contact' })
});

export const EditContact = withRouter(
	connect(
		mapStateToProps,
		mapDispatchToProps
	)(EditContactComp)
);
