import { types as t, Action } from '../actions';
import { Invoice } from '../classes/Invoice';

export const invoices = (state: Invoice[] = [], action: Action): Invoice[] => {
	switch (action.type) {
		case t.ARCHIVE_IMPORT_SUCCESS:
			return action.payload.invoices;
		case t.INVOICES_DELETE_SUCCESS:
		case t.INVOICES_FETCH_SUCCESS:
			return action.payload;
		case t.INVOICE_SAVE_SUCCESS: {
			const newState = [...state];
			const invoice = action.payload;
			const invoiceIndex = newState.findIndex(c => c.id === invoice.id);
			if (invoiceIndex > -1) {
				newState[invoiceIndex] = invoice;
			} else {
				newState.push(invoice);
			}
			return newState;
		}
		case t.INVOICE_SAVE_FAILURE:
			return state;
		default:
			return state;
	}
};
