import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { History } from 'history';
import { Table, Button, message } from 'antd';
import { ColumnProps } from 'antd/lib/table';
import { MessageType } from 'antd/lib/message';
import { Contact } from '../../classes/Contact';
import { ContactUIState } from '../../reducers/contactUI';

export interface ContactListProps {
	contacts: Contact[];
	history: History;
	onCreate: Function;
	onDelete: Function;
	ui: ContactUIState;
}

export class ContactList extends Component<ContactListProps> {
	private hideSavingProgressMessage: MessageType | null = null;

	state = {
		selectedRowKeys: [] // Check here to configure the default column
	};

	componentWillUpdate(nextProps: ContactListProps) {
		const { isSaving: wasSaving } = this.props.ui;
		const { isSaving } = nextProps.ui;
		if (!isSaving && wasSaving) {
			this.hideMessage();
			message.success('Zmeny boli uloen', 1);
			this.setState({ selectedRowKeys: [] });
		} else if (isSaving) {
			this.hideMessage();
			this.hideSavingProgressMessage = message.loading(
				'Prebieha ukladanie...',
				0
			);
		}
	}

	componentWillUnmount() {
		this.hideMessage();
	}

	hideMessage() {
		if (this.hideSavingProgressMessage) {
			this.hideSavingProgressMessage(); // hide message
		}
		this.hideSavingProgressMessage = null;
	}

	onSelectChange = (selectedRowKeys: any) => {
		this.setState({ selectedRowKeys });
	};

	onCreate = () => {
		this.props.onCreate(this.props.history);
	};

	onDelete = () => {
		const { selectedRowKeys } = this.state;
		this.props.onDelete(selectedRowKeys);
	};

	render() {
		const { contacts, ui } = this.props;
		const { selectedRowKeys } = this.state;
		const rowSelection = {
			selectedRowKeys,
			onChange: this.onSelectChange
		};
		const hasSelected = selectedRowKeys.length > 0;

		return (
			<div className="fz-list-page">
				<div className="fz-btn-group">
					<Button
						type="primary"
						icon="form"
						size="large"
						onClick={this.onCreate}
					>
						Prida
					</Button>
					<Button
						icon="delete"
						size="large"
						disabled={!hasSelected}
						onClick={this.onDelete}
					>
						Vymaza
					</Button>
				</div>

				<div className="fz-table-wrapper">
					<Table
						rowKey="id"
						className="fz-table fz-contact-table"
						dataSource={contacts}
						columns={columns}
						rowSelection={rowSelection}
						loading={ui.isFetching}
						// scroll={{ x: 'max-content' }}
						// onRow={(record, rowIndex) => {
						// 	return {
						// 		onClick: () => history.push(`/contacts/${record.key}`)
						// 	};
						// }}
					/>
				</div>
			</div>
		);
	}
}

const columns: ColumnProps<Contact>[] = [
	{
		title: 'Nzov',
		dataIndex: 'entity',
		key: 'entity',
		render: function contactLink(text: any, entity: Contact) {
			return <Link to={`/contacts/${entity.id}`}>{text}</Link>;
		}
	},
	{
		title: 'Meno',
		dataIndex: 'name',
		key: 'name'
	},
	{
		title: 'E-mail',
		dataIndex: 'email',
		key: 'email'
	},
	{
		title: 'Telefn',
		dataIndex: 'phoneNr',
		key: 'phoneNr'
	}
];
