import React, { Component } from 'react';
import {
	Form,
	Row,
	Col,
	Select,
	Input,
	Divider,
	DatePicker,
	Button,
	Icon
} from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { WrappedFormUtils } from 'antd/lib/form/Form';
import { InvoiceItems } from './InvoiceItems';
import { Discount } from './Discount';
import { PaymentTypeEnum } from '../../classes/Invoice';
import { InvoiceItem } from '../../classes/InvoiceItem';
import { Contact } from '../../classes/Contact';

const { TextArea } = Input;
const { Option } = Select;

const dateFormat = 'DD.MM.YYYY';

export interface InvoiceFormProps extends FormComponentProps {
	fields: any;
	items: InvoiceItem[];
	netPrice: number;
	grossPrice: number;
	discountPrice: number;
	contacts: Contact[];
	constantSymbols: [string, string][];
	isSaving: boolean;
	onItemRemove: (k: number) => void;
	onItemAdd: () => void;
	onEntityCreate: (isOwn: boolean) => void;
	onFieldsChange: (changedFields: any, props: InvoiceFormProps) => void;
	onValuesChange?: (values: any) => void;
	onSubmit: (e: React.FormEvent, form: WrappedFormUtils) => void;
	onPdfClick: () => void;
}

const formLayout = {
	labelCol: {
		xs: { span: 24 },
		sm: { span: 8 },
		lg: { span: 8 }
	},
	wrapperCol: {
		xs: { span: 24 },
		sm: { span: 16 },
		lg: { span: 16 }
	}
};

const columnLayout = {
	xs: 24,
	sm: 16,
	lg: 12
};

class InvoiceForm extends Component<InvoiceFormProps> {
	handleSubmit = (e: React.FormEvent) => {
		this.props.onSubmit(e, this.props.form);
	};

	handleEntityCreate = (e: React.SyntheticEvent) => {
		e.preventDefault();
		let isOwn = false;
		if (e.target instanceof HTMLElement) {
			const { type } = e.target.dataset;
			if (type === 'emitent') isOwn = true;
		}
		this.props.onEntityCreate(isOwn);
	};

	render() {
		const {
			items,
			netPrice,
			grossPrice,
			discountPrice,
			contacts,
			constantSymbols,
			isSaving,
			onItemAdd,
			onItemRemove,
			onPdfClick
		} = this.props;
		const { getFieldDecorator, getFieldValue } = this.props.form;

		return (
			<Form
				{...formLayout}
				layout="horizontal"
				className="fz-invoice-form"
				onSubmit={this.handleSubmit}
			>
				{getFieldDecorator('id', {})(<Input type="hidden" />)}
				{getFieldDecorator('type', {})(<Input type="hidden" />)}

				<Row className="fz-invoice-row">
					<Col {...columnLayout}>
						<Form.Item label="Odberate">
							{getFieldDecorator('recipient', {
								rules: [
									{
										required: true,
										message: 'Vyberte odberatea!'
									}
								]
							})(
								<Select>
									{contacts.map(contact => (
										<Option key={`rcpt_${contact.id}`} value={contact.id}>
											{contact.entity}
										</Option>
									))}
								</Select>
							)}
						</Form.Item>
					</Col>
					<Col {...columnLayout}>
						<a
							data-type="recipient"
							style={{ lineHeight: '40px' }}
							onClick={this.handleEntityCreate}
						>
							<Icon type="plus" />
							&nbsp;Nov odberate
						</a>
					</Col>
				</Row>

				<Divider />

				<Row className="fz-invoice-row">
					<Col {...columnLayout}>
						<Form.Item label="Dodvate">
							{getFieldDecorator('emitent', {
								rules: [
									{
										required: true,
										message: 'Vyberte dodvatea!'
									}
								]
							})(
								<Select>
									{contacts.reduce(
										(options, contact) => {
											if (contact.isOwn) {
												const option = (
													<Option key={`emt_${contact.id}`} value={contact.id}>
														{contact.entity}
													</Option>
												);
												options.push(option);
											}
											return options;
										},
										[] as any
									)}
								</Select>
							)}
						</Form.Item>
					</Col>
					<Col {...columnLayout}>
						<a
							data-type="emitent"
							style={{ lineHeight: '40px', paddingLeft: 12 }}
							onClick={this.handleEntityCreate}
						>
							<Icon type="plus" />
							&nbsp;Nov dodvate
						</a>
					</Col>
				</Row>

				<Divider />

				<Row className="fz-invoice-row">
					<Col {...columnLayout}>
						<Form.Item label="Spsob hrady">
							{getFieldDecorator('paymentType', {
								rules: [
									{
										required: true,
										message: 'Chbajci spsob hrady!'
									}
								]
							})(
								<Select>
									<Option value={PaymentTypeEnum.WireTransfer}>Prevodom</Option>
									<Option value={PaymentTypeEnum.Card}>Kartou</Option>
									<Option value={PaymentTypeEnum.Cash}>V hotovosti</Option>
									<Option value={PaymentTypeEnum.CashOnDelivery}>
										Dobierka
									</Option>
								</Select>
							)}
						</Form.Item>
						<Form.Item label="Dtum vystavenia">
							{getFieldDecorator('issueDate', {
								rules: [
									{
										required: true,
										message: 'Vyplte dtum vystavenia!'
									}
								]
							})(<DatePicker format={dateFormat} />)}
						</Form.Item>
						<Form.Item label="Dtum splatnosti">
							{getFieldDecorator('dueDate', {
								rules: [
									{
										required: true,
										message: 'Chbajci dtum splatnosti!'
									}
								]
							})(<DatePicker format={dateFormat} />)}
						</Form.Item>
						<Form.Item label="Dtum dodania">
							{getFieldDecorator('deliveryDate', {
								rules: [
									{
										required: false,
										message: 'Vyplte dtum dodania!'
									}
								]
							})(<DatePicker format={dateFormat} />)}
						</Form.Item>
					</Col>
					<Col {...columnLayout}>
						<Form.Item label="Variabiln symbol">
							{getFieldDecorator('variableSymbol', {
								rules: [
									{
										required: true,
										message: 'Vyplte variabiln symbol!'
									}
								]
							})(<Input />)}
						</Form.Item>
						<Form.Item label="Kontantn symbol">
							{getFieldDecorator('constantSymbol', {
								rules: [
									{
										required: true,
										message: 'Vyplte kontantn symbol!'
									}
								]
							})(
								<Select>
									{constantSymbols.map(([symbol, desc]) => (
										<Option key={symbol} value={symbol}>
											{symbol} - {desc}
										</Option>
									))}
								</Select>
							)}
						</Form.Item>
						<Form.Item label="slo objednvky">
							{getFieldDecorator('orderNr', {
								rules: [
									{
										required: false,
										message: 'Vyplte slo objednvky!'
									}
								]
							})(<Input />)}
						</Form.Item>
					</Col>
				</Row>
				<Row className="fz-invoice-row">
					<Col span={24}>
						<Form.Item label="Popis">
							{getFieldDecorator('description', {
								rules: [
									{
										required: false,
										message: 'Vyplte popis!'
									}
								]
							})(<Input placeholder="Popis objednvky" />)}
						</Form.Item>
					</Col>
				</Row>

				<Divider />
				<InvoiceItems
					items={items}
					type={getFieldValue('type')}
					getFieldDecorator={getFieldDecorator}
					onItemAdd={onItemAdd}
					onItemRemove={onItemRemove}
				/>

				<Discount
					netPrice={netPrice}
					grossPrice={grossPrice}
					discountPrice={discountPrice}
					getFieldDecorator={getFieldDecorator}
				/>

				<Row className="fz-invoice-row" type="flex" justify="start">
					<Col span={24}>
						<Form.Item label="Text na konci faktry">
							{getFieldDecorator('finalText', {
								rules: [
									{
										required: false,
										message: 'Chba text!'
									}
								]
							})(
								<TextArea
									autosize={{
										minRows: 4,
										maxRows: 4
									}}
								/>
							)}
						</Form.Item>
					</Col>
				</Row>

				<Row className="fz-invoice-row">
					<Col span={24} style={{ textAlign: 'right', margin: 15 }}>
						<div className="fz-btn-group">
							<Button icon="file-pdf" size="large" onClick={onPdfClick}>
								PDF
							</Button>
							<Button
								type="primary"
								icon="save"
								size="large"
								htmlType="submit"
								loading={isSaving}
							>
								Uloi
							</Button>
						</div>
					</Col>
				</Row>
			</Form>
		);
	}
}

export const EnhancedInvoiceForm = Form.create({
	name: 'invoice',
	onFieldsChange(props: InvoiceFormProps, changedFields, allFields: any) {
		props.onFieldsChange(changedFields, allFields);
	},
	mapPropsToFields(props: InvoiceFormProps) {
		const { fields } = props;

		if (Object.keys(fields).length === 0) {
			return {};
		}

		return {
			id: Form.createFormField(fields.id),
			type: Form.createFormField(fields.type),
			recipient: Form.createFormField(fields.recipient),
			emitent: Form.createFormField(fields.emitent),
			paymentType: Form.createFormField(fields.paymentType),
			dueDate: Form.createFormField(fields.dueDate),
			issueDate: Form.createFormField(fields.issueDate),
			deliveryDate: Form.createFormField(fields.deliveryDate),
			paymentDate: Form.createFormField(fields.paymentDate),
			isPayed: Form.createFormField({
				value: !!fields.paymentDate.value
			}),
			variableSymbol: Form.createFormField(fields.variableSymbol),
			constantSymbol: Form.createFormField(fields.constantSymbol),
			orderNr: Form.createFormField(fields.orderNr),
			description: Form.createFormField(fields.description),
			finalText: Form.createFormField(fields.finalText),
			itemsNames:
				fields.itemsNames &&
				fields.itemsNames.map((field: any) => Form.createFormField(field)),
			pricesPU:
				fields.pricesPU &&
				fields.pricesPU.map((field: any) => Form.createFormField(field)),
			units:
				fields.units &&
				fields.units.map((field: any) => Form.createFormField(field)),
			amounts:
				fields.amounts &&
				fields.amounts.map((field: any) => Form.createFormField(field)),
			taxRates:
				fields.taxRates &&
				fields.taxRates.map((field: any) => Form.createFormField(field)),
			grossPrices:
				fields.grossPrices &&
				fields.grossPrices.map((field: any) => Form.createFormField(field)),
			taxPrices:
				fields.taxPrices &&
				fields.taxPrices.map((field: any) => Form.createFormField(field)),
			netPrices:
				fields.netPrices &&
				fields.netPrices.map((field: any) => Form.createFormField(field)),
			discount: Form.createFormField(fields.discount),
			grossPrice: Form.createFormField(fields.grossPrice),
			discountPrice: Form.createFormField(fields.discountPrice),
			netPrice: Form.createFormField(fields.netPrice)
		};
	},
	onValuesChange(props: InvoiceFormProps, values) {
		props.onValuesChange && props.onValuesChange(values);
	}
})(InvoiceForm);
