import React, { Component } from 'react';
import { RouteComponentProps } from 'react-router';
import { Modal, Card, Button, Upload, Icon, message } from 'antd';
import { MergeResult } from '../../api';
import { UIState } from '../../reducers/ui';
import { Invoice } from '../../classes/Invoice';
import { Contact } from '../../classes/Contact';
import './style.css';

type MergeResults = [MergeResult<Contact>, MergeResult<Invoice>];

export interface ArchiveProps extends RouteComponentProps {
	ui: UIState;
	onArchiveExport: () => Promise<void>;
	onArchiveImport: (archive: string) => Promise<MergeResults>;
}

export class Archive extends Component<ArchiveProps> {
	showConflicts = (mergeResults: MergeResults) => {
		const groups = ['Kontakt', 'Faktra'];
		const rows = mergeResults.reduce(
			(memo, result, i) => {
				const group = groups[i];
				const { items, conflicts } = result as any;
				const rows = conflicts.map((conflict: any) => {
					const item = items.find((doc: any) => doc.id === conflict.id);
					return (
						<tr key={item.id} className="fz-overview-table__row">
							<td className="fz-overview-table__row__cell fz-overview-table__row__cell--center fz-overview-table__row__cell--nowrap">
								{group === 'Kontakt' ? conflict.entity : item.id}
							</td>
							<td className="fz-overview-table__row__cell fz-overview-table__row__cell--center">
								{item.revision || '0'}
							</td>
							<td className="fz-overview-table__row__cell fz-overview-table__row__cell--center">
								{conflict.revision || '0'}
							</td>
							<td className="fz-overview-table__row__cell fz-overview-table__row__cell--right">
								<Icon
									type="stop"
									style={{ fontSize: '16px', color: 'red' }}
									theme="outlined"
								/>
							</td>
						</tr>
					);
				});
				if (rows.length > 0) {
					rows.unshift(
						<tr
							key={group}
							className="fz-overview-table__row fz-overview-table__row--group"
						>
							<td className="fz-overview-table__row__cell" colSpan={4}>
								<strong>{group}</strong>
							</td>
						</tr>
					);
				}
				return memo.concat(rows);
			},
			[] as any
		);
		const content = (
			<div>
				<div className="fz-overview-table-wrapper">
					<table className="fz-overview-table">
						<colgroup>
							<col width="100" />
							<col width="20" />
							<col width="20" />
							<col width="20" />
						</colgroup>
						<thead>
							<tr className="fz-overview-table__row fz-overview-table__row--header">
								<th className="fz-overview-table__row__cell">Id.</th>
								<th className="fz-overview-table__row__cell">AV*</th>
								<th className="fz-overview-table__row__cell">IV**</th>
								<th className="fz-overview-table__row__cell" />
							</tr>
						</thead>
						<tbody>{rows}</tbody>
					</table>
				</div>
				<ul className="fz-explanatory-notes">
					<li>* AV - aktulna verzia</li>
					<li>** IV - verzia importovanho dokumentu</li>
				</ul>
			</div>
		);

		Modal.warning({
			width: 540,
			title: 'Nasledovn poloky boli vynechan z importu.',
			content
		});
	};

	handleExport = () => {
		const hideMsg = message.loading('Vykonvam zlohu...', 0);
		this.props
			.onArchiveExport()
			.then(() => {
				hideMsg();
			})
			.catch(error => {
				hideMsg();
				message.error(error.message);
			});
	};

	handleImport = (file: any) => {
		const hideMsg = message.loading('Obnovujem zo zlohy...', 0);
		new Promise((resolve, reject) => {
			const reader = new FileReader();
			reader.onerror = (e: Event) => {
				reject(e);
			};
			reader.onload = (e: Event) => {
				resolve(reader.result as any);
			};
			reader.readAsText(file);
		})
			.then((archive: any) => {
				this.props.onArchiveImport(archive as string).then(result => {
					hideMsg();
					const [contacts, invoices] = result;
					const conflicts =
						contacts.conflicts.length + invoices.conflicts.length;
					if (conflicts) {
						this.showConflicts(result);
					} else {
						message.success('Archv bol naimportovan');
					}
				});
			})
			.catch(error => {
				hideMsg();
				message.error('Import zlyhal');
			});

		return false;
	};

	render() {
		return (
			<div>
				<Card style={{ marginBottom: 20 }}>
					<p>
						Archivcia databzy do sboru. Odpora sa vykonva tto innos
						pravidelne.
					</p>
					<div>
						<Button onClick={this.handleExport}>
							<Icon type="download" /> Archivuj do sboru
						</Button>
					</div>
				</Card>
				<Card>
					<p>Import databzy z archvu.</p>
					<p>
						Existujce kontakty a faktry bud zmenen len v prpade ak archv
						obsahuje ich noviu verziu.
					</p>
					<div>
						<Upload
							accept=".json"
							beforeUpload={this.handleImport}
							showUploadList={false}
						>
							<Button>
								<Icon type="upload" /> Nataj z archvu
							</Button>
						</Upload>
					</div>
				</Card>
			</div>
		);
	}
}
