import StringMask from 'string-mask';
import { isValidIBANNumber } from './iban';

const locale = 'sk-SK';

export const currencyFormat = new Intl.NumberFormat(locale, {
	style: 'currency',
	currency: 'EUR'
});
export const dateFormat = Intl.DateTimeFormat(locale);

export const formatIBAN = (value: string): string => {
	if (isValidIBANNumber(value) === 1) {
		const mask = new StringMask('UUAA AAAA AAAA AAAA AAAA AAAA');
		const { result, valid } = mask.process(value);
		return valid ? result : value;
	}
	return value;
};

export const formatZipCode = (value: string): string => {
	if (typeof value !== 'string') {
		return value;
	}
	const alphaNumValue = value.toUpperCase().replace(/[^A-Z0-9]/g, '');
	if (alphaNumValue.length !== 5) {
		return value;
	}
	const mask = new StringMask('000 00');
	const { result, valid } = mask.process(alphaNumValue);
	return valid ? result : value;
};
