import React, { Component } from 'react';
import { Route, Switch, withRouter } from 'react-router-dom';
import { Layout, Menu } from 'antd';
import { VisibleInvoiceList } from './containers/VisibleInvoiceList';
import { VisibleContactList } from './containers/VisibleContactList';
import { EditInvoice } from './containers/EditInvoice';
import { EditContact } from './containers/EditContact';
import { Archive } from './containers/Archive';
import { ClickParam } from 'antd/lib/menu';
import './App.css';

const { Header, Content, Footer } = Layout;

export const routes = [
	{
		exact: true,
		path: '/invoices/:id',
		component: EditInvoice
	},
	{
		exact: true,
		path: '/invoices',
		component: VisibleInvoiceList
	},
	{
		exact: true,
		path: '/contacts/:id',
		component: EditContact
	},
	{
		exact: true,
		path: '/contacts',
		component: VisibleContactList
	},
	{
		exact: true,
		path: '/archive',
		component: Archive
	}
];

const menuItems = [
	{
		key: 'invoices',
		name: 'Faktry'
	},
	{
		key: 'contacts',
		name: 'Kontakty'
	},
	{
		key: 'archive',
		name: 'Archv'
	}
];

class App extends Component<any, any> {
	onMenuClicked = ({ key }: ClickParam) => {
		const { history } = this.props;
		history.push(`/${key}`);
	};

	render() {
		const { pathname } = this.props.location;
		const selectedMenu = menuItems.find(item => pathname.includes(item.key));
		const selectedMenuKey = (selectedMenu && selectedMenu.key) || '';

		return (
			<Layout className="fz-layout">
				<Header className="fz-layout-header">
					<Menu
						className="fz-layout-header-menu"
						theme="dark"
						mode="horizontal"
						selectedKeys={[selectedMenuKey]}
						onClick={this.onMenuClicked}
					>
						{menuItems.map(({ key, name }) => (
							<Menu.Item key={key}>{name}</Menu.Item>
						))}
					</Menu>
					<div className="fz-logo" />
				</Header>
				<Content className="fz-layout-content">
					<Switch>
						{routes.map((route, i) => (
							<Route key={i} {...route} />
						))}
					</Switch>
				</Content>
				<Footer className="fz-layout-footer">
					<div className="fz-footer-brand">
						<span>
							2019 <a href="https://fakturazdarma.sk/">FakturaZdarma.sk</a>
						</span>
					</div>
					<div>
						<a href="https://www.facebook.com/FakturaZdarma/">
							<div className="fcb-find-us" />
						</a>
					</div>
				</Footer>
			</Layout>
		);
	}
}

export default withRouter(App);
