import { Contact } from './Contact';
import { InvoiceItem } from './InvoiceItem';

const DAY_IN_MILLIS = 24 * 3600 * 1000;

export enum PaymentTypeEnum {
	WireTransfer = 1,
	Card,
	Cash,
	CashOnDelivery
}

export enum InvoiceType {
	NonTax = 1,
	Taxable
}

export class Invoice {
	id?: string;
	type: InvoiceType = InvoiceType.NonTax;
	description?: string;
	paymentType: PaymentTypeEnum = PaymentTypeEnum.WireTransfer;
	recipient: Contact | undefined;
	emitent: Contact | undefined;
	items: InvoiceItem[] = [];
	discount: number = 0;
	variableSymbol: string = '';
	constantSymbol: string = '0308';
	orderNr?: string;
	issueDate: Date;
	dueDate: Date;
	deliveryDate?: Date;
	paymentDate?: Date;
	finalText?: string;
	revision?: number;
	updatedAt?: Date;

	constructor(invoice?: Partial<Invoice>) {
		const now = Date.now();

		Object.assign(this, invoice);

		// @ts-ignore: this.prop is really assigned before being used
		this.issueDate = new Date(this.issueDate || now);
		// @ts-ignore: this.prop is really assigned before being used
		this.dueDate = new Date(this.dueDate || now + 15 * DAY_IN_MILLIS);
		this.paymentDate = this.paymentDate
			? new Date(this.paymentDate)
			: undefined;
		this.deliveryDate = this.deliveryDate
			? new Date(this.deliveryDate)
			: undefined;

		this.items =
			invoice && invoice.items
				? invoice.items.map(item => new InvoiceItem(item))
				: [];
	}

	get grossPrice(): number {
		if (this.type === InvoiceType.NonTax) {
			return this.items.reduce((price, item) => {
				return (price += item.netPrice);
			}, 0);
		}
		if (this.type === InvoiceType.Taxable) {
			const taxGroups = this.items.reduce<{ [taxRate: string]: number }>(
				(taxGroups, item) => {
					const { taxRate, netPrice } = item;
					taxGroups[taxRate] = (taxGroups[taxRate] || 0) + netPrice;
					return taxGroups;
				},
				{}
			);
			return Object.entries(taxGroups).reduce(
				(grossPrice, [taxRate, netPrice]) => {
					const tax = (netPrice / 100) * Number(taxRate);
					grossPrice += netPrice + tax;
					return grossPrice;
				},
				0
			);
		}
		return Number.NaN;
	}

	get discountPrice(): number {
		return -(this.grossPrice / 100) * this.discount;
	}

	get netPrice(): number {
		const { grossPrice, discountPrice } = this;
		return grossPrice + discountPrice;
	}
}
