import { types as t, Action } from '../actions';

export type ContactUIState = {
	contactId: string | null;
	isFetching: boolean;
	isSaving: boolean;
	isBankExpanded: boolean;
	isContactExpanded: boolean;
	fields: any;
};

const initialState = {
	contactId: null,
	isFetching: false,
	isSaving: false,
	isBankExpanded: false,
	isContactExpanded: false,
	fields: {}
};

const fields = (state: any, action: Action) => {
	switch (action.type) {
		case t.CONTACT_SAVE_SUCCESS: {
			return {};
		}
		case t.CONTACT_FORM_REPLACE: {
			return action.payload;
		}
		default:
			return state;
	}
};

const ui = (
	state: ContactUIState = initialState,
	action: Action
): ContactUIState => {
	switch (action.type) {
		case t.CONTACT_FETCH:
			return { ...state, isFetching: true };
		case t.CONTACT_FETCH_SUCCESS:
			return { ...state, isFetching: false };
		case t.CONTACT_FETCH_FAILURE:
			return { ...state, isFetching: false };
		case t.CONTACT_SAVE:
			return { ...state, isSaving: true };
		case t.CONTACT_SAVE_SUCCESS:
			return { ...state, isSaving: false };
		case t.CONTACT_SAVE_FAILURE:
			return { ...state, isSaving: false };
		case t.CONTACT_FORM_TOGGLE: {
			const section = action.payload;
			const newState = { ...state };
			switch (section) {
				case 'bank':
					newState.isBankExpanded = !state.isBankExpanded;
					break;
				case 'contact':
					newState.isContactExpanded = !state.isContactExpanded;
					break;
				default:
			}
			return newState;
		}
		case t.CONTACT_FORM_REPLACE:
			return { ...state };
		default:
			return state;
	}
};

export const contactUI = (
	state: ContactUIState = initialState,
	action: Action
): ContactUIState => ({
	...ui(state, action),
	fields: fields(state.fields, action)
});
