import { History } from 'history';
import { withRouter } from 'react-router-dom';
import { AnyAction } from 'redux';
import { ThunkDispatch } from 'redux-thunk';
import { connect } from 'react-redux';
import {
	addItem,
	removeItem,
	fetchInvoiceForm,
	saveInvoice,
	createEntity,
	changeInvoiceId,
	updateInvoiceForm,
	checkInvoiceExists
} from '../actions';
import { EditInvoice as EditInvoiceComp } from '../components/EditInvoice';
import { AppState } from '../reducers';
import { InvoiceMapper } from '../classes/InvoiceMapper';

export const selectInvoice = (state: AppState, ownProps: any) => {
	const { params } = ownProps.match;
	const { invoices } = state;
	return invoices.find(invoice => invoice.id === params.id);
};

export const selectInvoiceForm = (state: AppState, ownProps: any) => {
	const { invoiceUI } = state;
	const { fields, itemKeys } = invoiceUI;
	const { id } = ownProps.match.params;

	if (fields.id && fields.id.value === id) {
		return { fields, itemKeys };
	}
	return { fields: {}, itemKeys: [] };
};

const mapStateToProps = (state: AppState, ownProps: any) => {
	const { match } = ownProps;
	const { id } = match.params;
	const form: any = selectInvoiceForm(state, ownProps);
	const invoice = new InvoiceMapper().fromFields(form.fields, form.itemKeys);

	return {
		...state.invoiceUI,
		...form,
		invoiceId: id,
		items: invoice.items,
		netPrice: invoice.netPrice,
		grossPrice: invoice.grossPrice,
		discountPrice: invoice.discountPrice,
		contacts: state.contacts
	};
};

const mapDispatchToProps = (
	dispatch: ThunkDispatch<AppState, null, AnyAction>
) => ({
	fetchForm: (id: string) => dispatch(fetchInvoiceForm(id)),
	onFieldsChange: (changedFields: any, allFields: any) =>
		dispatch(updateInvoiceForm(changedFields, allFields)),
	onItemAdd: (invoiceId: string) => dispatch(addItem(invoiceId)),
	onItemRemove: (invoiceId: string, k: number) =>
		dispatch(removeItem(invoiceId, k)),
	onSave: (values: any, itemKeys: number[]) =>
		dispatch(saveInvoice(values, itemKeys)),
	onInvoiceChangeId: (
		newId: string,
		values: any,
		itemKeys: number[],
		routerHistory: History
	) => dispatch(changeInvoiceId(newId, values, itemKeys, routerHistory)),
	onInvoiceCheckExists: (id: string) => dispatch(checkInvoiceExists(id)),
	onEntityCreate: (
		values: any,
		itemKeys: number[],
		isOwn: boolean,
		routerHistory: History
	) => dispatch(createEntity(values, itemKeys, isOwn, routerHistory))
});

// export type EditInvoiceProps = ReturnType<typeof mapStateToProps> &
// 	ReturnType<typeof mapDispatchToProps> & {};

export const EditInvoice = withRouter(
	connect(
		mapStateToProps,
		mapDispatchToProps
	)(EditInvoiceComp)
);
