import React, { FunctionComponent } from 'react';
import classNames from 'classnames';
import { Form, Row, Col, Input, InputNumber, Button, Icon } from 'antd';
import { TextField } from './TextField';
import { InvoiceType } from '../../classes/Invoice';
import { InvoiceItem } from '../../classes/InvoiceItem';
import { currencyFormat } from '../../utils/formatters';

export interface InvoiceItemsProps {
	items: InvoiceItem[];
	type: InvoiceType;
	getFieldDecorator: any;
	onItemRemove: (k: number) => void;
	onItemAdd: () => void;
}

const FormItem = ({ children, ...props }: any) => {
	return (
		<Form.Item
			{...props}
			className={classNames(
				'fz-invoice-items__row__cell__item',
				props.className
			)}
			labelCol={{
				xs: 24,
				sm: 8,
				prefixCls: 'fz-invoice-items__label'
			}}
			wrapperCol={{
				xs: 24,
				sm: 16,
				prefixCls: 'fz-invoice-items__wrapper'
			}}
		>
			{children}
		</Form.Item>
	);
};

export const InvoiceItems: FunctionComponent<InvoiceItemsProps> = props => {
	const { type, items, onItemAdd, onItemRemove, getFieldDecorator } = props;

	let index = 0;

	return (
		<>
			<div className="fz-invoice-row">
				<h4 className="fz-invoice-header">Poloky faktry</h4>
			</div>
			<div className="fz-invoice-items">
				{items.map((item, k: number) => {
					const { netPrice = 0, taxPrice = 0, grossPrice = 0 } = items[k] || {};
					return (
						<div
							key={k}
							className={`fz-invoice-items__row${
								index++ === 0 ? ' fz-invoice-items__row--header' : ''
							}`}
						>
							<div className="fz-invoice-items__row__cell fz-invoice-items__row__cell--delete">
								<FormItem label="&nbsp;" colon={false}>
									{Object.keys(items).length > 1 ? (
										<Icon
											className="fz-dynamic-delete-button"
											type="minus-circle-o"
											onClick={() => onItemRemove(k)}
										/>
									) : null}
								</FormItem>
							</div>

							<div className="fz-invoice-items__row__cell-group fz-invoice-items__row__cell-group__name">
								<div className="fz-invoice-items__row__cell fz-invoice-items__row__cell--name">
									<FormItem label="Poloka">
										{getFieldDecorator(`itemsNames[${k}]`, {
											validateTrigger: ['onChange', 'onBlur'],
											rules: [
												{
													required: true,
													whitespace: true,
													message: 'Vyplte nzov poloky!'
												}
											]
										})(<Input />)}
									</FormItem>
								</div>
							</div>

							<div className="fz-invoice-items__row__cell-group">
								<div className="fz-invoice-items__row__cell fz-invoice-items__row__cell--narrow">
									<FormItem label="Jednotka">
										{getFieldDecorator(`units[${k}]`, {
											validateTrigger: ['onChange', 'onBlur'],
											rules: [
												{
													required: true,
													whitespace: true,
													message: 'Vyplte jednotku!'
												}
											]
										})(<Input />)}
									</FormItem>
								</div>
								<div className="fz-invoice-items__row__cell fz-invoice-items__row__cell--narrow">
									<FormItem label="Mnostvo">
										{getFieldDecorator(`amounts[${k}]`, {
											validateTrigger: ['onChange'],
											rules: [
												{
													type: 'number',
													min: 0,
													required: true,
													whitespace: true,
													message: 'Vyplte mnostvo!'
												}
											]
										})(
											<InputNumber
												style={{ width: 'auto' }}
												min={0}
												decimalSeparator=","
											/>
										)}
									</FormItem>
								</div>
								<div className="fz-invoice-items__row__cell fz-invoice-items__row__cell--price">
									<FormItem label="Cena za m.j.">
										{getFieldDecorator(`pricesPU[${k}]`, {
											validateTrigger: ['onChange'],
											rules: [
												{
													type: 'number',
													min: 0,
													required: true,
													whitespace: true,
													message: 'Vyplte cenu!'
												}
											]
										})(
											<InputNumber
												style={{ width: 'auto' }}
												min={0}
												decimalSeparator=","
											/>
										)}
									</FormItem>
								</div>
								{type === InvoiceType.Taxable ? (
									<div className="fz-invoice-items__row__cell fz-invoice-items__row__cell--narrow">
										<FormItem label="DPH %">
											{getFieldDecorator(`taxRates[${k}]`, {
												validateTrigger: ['onChange'],
												rules: [
													{
														type: 'number',
														min: 0,
														max: 100,
														required: true,
														whitespace: true,
														message: 'Vyplte DPH!'
													}
												]
											})(
												<InputNumber
													style={{ width: 'auto' }}
													min={0}
													max={100}
													decimalSeparator=","
												/>
											)}
										</FormItem>
									</div>
								) : null}
							</div>

							<div className="fz-invoice-items__row__cell-group fz-invoice-items__row__cell-group__sums">
								{type === InvoiceType.Taxable ? (
									<>
										<div className="fz-invoice-items__row__cell fz-invoice-items__row__cell--sum">
											<FormItem label="bez DPH">
												<TextField value={currencyFormat.format(netPrice)} />
											</FormItem>
										</div>
										<div className="fz-invoice-items__row__cell fz-invoice-items__row__cell--sum">
											<FormItem label="DPH">
												<TextField value={currencyFormat.format(taxPrice)} />
											</FormItem>
										</div>
										<div className="fz-invoice-items__row__cell fz-invoice-items__row__cell--sum">
											<FormItem label="s DPH">
												<TextField value={currencyFormat.format(grossPrice)} />
											</FormItem>
										</div>
									</>
								) : (
									<div className="fz-invoice-items__row__cell fz-invoice-items__row__cell--sum">
										<FormItem label="Suma">
											<TextField value={currencyFormat.format(netPrice)} />
										</FormItem>
									</div>
								)}
							</div>
						</div>
					);
				})}
				<Row>
					<Col span={16} offset={1}>
						<Form.Item {...formItemLayoutWithOutLabel}>
							<Button
								type="dashed"
								onClick={onItemAdd}
								style={{ width: '60%' }}
							>
								<Icon type="plus" /> Pridaj poloku
							</Button>
						</Form.Item>
					</Col>
				</Row>
			</div>
		</>
	);
};

const formItemLayoutWithOutLabel = {
	wrapperCol: {
		xs: { span: 24, offset: 0 },
		sm: { span: 20, offset: 0 }
	}
};
