export class InvoiceItem {
	name: string = '';
	unit: string = 'kus';
	pricePerUnit: number = 0;
	amount: number = 0;
	taxRate: number = 20;

	constructor(item?: Partial<InvoiceItem>) {
		Object.assign(this, item);
	}

	get netPrice(): number {
		return this.pricePerUnit * this.amount;
	}

	get grossPrice(): number {
		return this.netPrice + this.taxPrice;
	}

	get taxPrice(): number {
		return (this.netPrice / 100) * this.taxRate;
	}
}
