import moment from 'moment';
import * as t from './actionTypes';
import { fetchContacts } from './contact';
import { fetchInvoices } from './invoice';
import client from '../api';
import { saveBlob } from '../utils/blob';

const DATE_FORMAT = 'YYYY-MM-DD_HH-mm-ss';

export const importArchive = (archive: string) => (dispatch: any) => {
	dispatch({ type: t.ARCHIVE_IMPORT, payload: {} });
	return new Promise((resolve, reject) => {
		try {
			const json = JSON.parse(archive);
			if (!(Array.isArray(json.contacts) && Array.isArray(json.invoices))) {
				throw new Error('Neplatn typ archvu');
			}
			resolve(json);
		} catch (error) {
			reject(error);
		}
	})
		.then((json: any) =>
			Promise.all([
				client.mergeContacts(json.contacts),
				client.mergeInvoices(json.invoices)
			])
		)
		.then(results => {
			const [contactsResult, invoicesResult] = results;
			dispatch({
				type: t.ARCHIVE_IMPORT_SUCCESS,
				payload: {
					contacts: contactsResult.items,
					invoices: invoicesResult.items
				}
			});
			return results;
		})
		.catch(error => {
			console.log(error);
			dispatch({
				type: t.ARCHIVE_IMPORT_FAILURE,
				payload: new Error('Nastala chyba pri importe archvu!')
			});
			throw error;
		});
};

export const exportArchive = () => (dispatch: any) => {
	dispatch({ type: t.ARCHIVE_EXPORT, payload: {} });
	return Promise.all([dispatch(fetchContacts()), dispatch(fetchInvoices())])
		.then(([contacts, invoices]) => {
			dispatch({ type: t.ARCHIVE_EXPORT_SUCCESS, payload: {} });
			const blob = new Blob([JSON.stringify({ contacts, invoices })], {
				type: 'application/json'
			});
			saveBlob(blob, `fz_${moment().format(DATE_FORMAT)}.json`);
		})
		.catch(error => {
			dispatch({ type: t.ARCHIVE_EXPORT_FAILURE, payload: error });
			throw error;
		});
};
