export class Contact {
	id?: string;
	entity: string = '';
	name?: string;
	street?: string;
	city?: string;
	zipCode?: string;
	country?: string;
	legalId?: string;
	taxId?: string;
	taxLegalId?: string;
	iban?: string;
	bankAccount?: string;
	bankName?: string;
	bankCode?: string;
	swift?: string;
	email?: string;
	phoneNr?: string;
	isOwn?: boolean = false;
	revision?: number;
	updatedAt?: Date;

	constructor(contact?: Partial<Contact>) {
		Object.assign(this, contact);
	}

	get isTaxable() {
		return !!this.taxLegalId;
	}
}
