import { History } from 'history';
import { connect } from 'react-redux';
import { VisibilityFilters, createContact, deleteContacts } from '../actions';
import { ContactList } from '../components/ContactList';

const getVisibleContacts = (contacts: any, filter: any) => {
	switch (filter) {
		case VisibilityFilters.SHOW_ALL:
			return contacts;
		case VisibilityFilters.SHOW_COMPLETED:
			return contacts.filter((c: any) => c.completed);
		case VisibilityFilters.SHOW_ACTIVE:
			return contacts.filter((c: any) => !c.completed);
		default:
			return contacts;
	}
};

const mapStateToProps = (state: any) => {
	const contacts = getVisibleContacts(state.contacts, state.visibilityFilter);
	return {
		contacts: [...contacts],
		ui: state.ui
	};
};

const mapDispatchToProps = (dispatch: any) => {
	return {
		onCreate: (routerHistory: History) => {
			dispatch(createContact(routerHistory));
		},
		onDelete: (keys: string[]) => {
			dispatch(deleteContacts(keys));
		}
	};
};

export const VisibleContactList = connect(
	mapStateToProps,
	mapDispatchToProps
)(ContactList);
