import { Contact } from './Contact';

export class ContactMapper {
	toFields(contact: Contact) {
		return {
			id: { value: contact.id },
			entity: { value: contact.entity },
			street: { value: contact.street },
			zipCode: { value: contact.zipCode },
			city: { value: contact.city },
			country: { value: contact.country },
			legalId: { value: contact.legalId },
			taxId: { value: contact.taxId },
			taxLegalId: { value: contact.taxLegalId },
			bankName: { value: contact.bankName },
			bankCode: { value: contact.bankCode },
			swift: { value: contact.swift },
			iban: { value: contact.iban },
			bankAccount: { value: contact.bankAccount },
			name: { value: contact.name },
			email: { value: contact.email },
			phoneNr: { value: contact.phoneNr },
			isOwn: { value: !!contact.isOwn }
		};
	}

	fromFields(fields: any) {
		const contact = Object.entries(fields).reduce(
			(memo, [key, val]: [string, any]) => {
				if (val && val.value) {
					memo[key] = val.value;
				}
				return memo;
			},
			{} as any
		);
		return new Contact(contact);
	}
}
