import { History } from 'history';
import { Action } from 'redux';
import { ThunkAction } from 'redux-thunk';
import client from '../api';
import { AppState } from '../reducers';
import * as t from './actionTypes';
import { Contact } from '../classes/Contact';
import { NotFoundException } from '../classes/NotFoundException';
import { ContactMapper } from '../classes/ContactMapper';
import { dynamicSort } from '../utils/sort';

export type ContactAction<R> = ThunkAction<R, AppState, null, Action<string>>;

export const createContact = (
	routerHistory: History,
	isOwn: boolean = false
): ContactAction<Promise<number>> => (dispatch, getState) => {
	const { contacts } = getState();
	let newContactId = 1;
	const contactsSortedByKeyDesc = [...contacts].sort(dynamicSort('-id'));
	if (contactsSortedByKeyDesc[0]) {
		newContactId = Number(contactsSortedByKeyDesc[0].id) + 1;
	}
	routerHistory.push(
		`/contacts/${newContactId}${isOwn ? `?isOwn=${isOwn}` : ''}`
	);
	return Promise.resolve(newContactId);
};

export const fetchContacts = (): ContactAction<
	Promise<Contact[]>
> => dispatch => {
	dispatch({ type: t.CONTACTS_FETCH, payload: {} });
	return client.fetchContacts().then(
		contacts => {
			dispatch({
				type: t.CONTACTS_FETCH_SUCCESS,
				payload: contacts
			});
			return contacts;
		},
		error => {
			dispatch({
				type: t.CONTACTS_FETCH_FAILURE,
				payload: error
			});
			// Rethrow so returned Promise is rejected
			throw error;
		}
	);
};

export const fetchContact = (
	id: string
): ContactAction<Promise<Contact>> => dispatch => {
	dispatch({ type: t.CONTACT_FETCH, payload: {} });
	return client
		.fetchContact(id)
		.then(contact => {
			if (!contact) {
				throw new NotFoundException(`Kontakt \`${id}\` sa nenaiel!`);
			}
			dispatch({
				type: t.CONTACT_FETCH_SUCCESS,
				payload: contact
			});
			return contact;
		})
		.catch(error => {
			dispatch({
				type: t.CONTACT_FETCH_FAILURE,
				payload: error
			});
			// Rethrow so returned Promise is rejected
			throw error;
		});
};

export const updateContactForm = (
	changedFields: any,
	allFields: any
): ContactAction<void> => (dispatch, getState) => {
	const { bankList } = getState();

	if (changedFields.bankIndex) {
		const [bankName, bankCode, swift] = bankList[changedFields.bankIndex.value];
		allFields = {
			...allFields,
			bankName: { value: bankName },
			bankCode: { value: bankCode },
			swift: { value: swift }
		};
	}
	dispatch({ type: t.CONTACT_FORM_REPLACE, payload: allFields });
};

export const fetchContactForm = (id: string): ContactAction<void> => (
	dispatch,
	getState
) => {
	const { contacts } = getState();
	const contact = contacts.find((contact: Contact) => contact.id === id);

	if (!contact) {
		return dispatch(fetchContact(id)).then(
			contact => {
				const fields = new ContactMapper().toFields(contact);
				dispatch(updateContactForm({}, fields));
			},
			error => {
				if (error instanceof NotFoundException) {
					const contact = new Contact({ id, country: 'Slovensko' });
					const fields = new ContactMapper().toFields(contact);
					return dispatch(updateContactForm({}, fields));
				}
				throw error;
			}
		);
	}

	const fields = new ContactMapper().toFields(contact);
	return dispatch(updateContactForm({}, fields));
};

export const saveContact = (
	values: any,
	routerHistory: History
): ContactAction<Promise<Contact>> => dispatch => {
	dispatch({ type: t.CONTACT_SAVE, payload: {} });
	return client.saveContact(new Contact(values)).then(
		contact => {
			contact = new Contact(contact);
			dispatch({
				type: t.CONTACT_SAVE_SUCCESS,
				payload: contact
			});
			if (routerHistory.length) {
				routerHistory.goBack();
			} else {
				routerHistory.replace('/contacts');
			}

			return contact;
		},
		error => {
			dispatch({
				type: t.CONTACT_SAVE_FAILURE,
				payload: error
			});
			// Rethrow so returned Promise is rejected
			throw error;
		}
	);
};

export const deleteContacts = (
	keys: string[]
): ContactAction<void> => dispatch => {
	dispatch({ type: t.CONTACTS_DELETE, payload: keys });
	return client.deleteContacts(keys).then(
		contacts => {
			dispatch({
				type: t.CONTACTS_DELETE_SUCCESS,
				payload: contacts
			});
			// routerHistory.replace('/');
		},
		error => {
			dispatch({
				type: t.CONTACTS_DELETE_FAILURE,
				payload: error
			});
			// Rethrow so returned Promise is rejected
			throw error;
		}
	);
};
