import { ApiClient } from './ApiClient';
import { LocalStorageClient } from './LocalStorageClient';
import { DelayProxyBuilder } from './DelayProxy';

const client = new DelayProxyBuilder<ApiClient>()
	.setClient(LocalStorageClient.getInstance())
	.setDelay(500)
	.skipMethods([/.*Sync/])
	.build();

export default client;
export * from './ApiClient';
