import { types as t, Action } from '../actions';
import { Contact } from '../classes/Contact';

export const contacts = (state: Contact[] = [], action: Action): Contact[] => {
	switch (action.type) {
		case t.ARCHIVE_IMPORT_SUCCESS:
			return action.payload.contacts;
		case t.CONTACTS_DELETE_SUCCESS:
		case t.CONTACTS_FETCH_SUCCESS:
			return action.payload;
		case t.CONTACT_SAVE_SUCCESS: {
			const newState = [...state];
			const contact = action.payload;
			const contactIndex = newState.findIndex(c => c.id === contact.id);
			if (contactIndex > -1) {
				newState[contactIndex] = contact;
			} else {
				newState.push(contact);
			}
			return newState;
		}
		default:
			return state;
	}
};
