import { ApiClient, MergeResult } from './ApiClient';
import { Contact } from '../classes/Contact';
import { Invoice } from '../classes/Invoice';
import { mockInvoices, mockContacts } from './mocks';

type Document = {
	id?: string;
	revision?: number;
	updatedAt?: Date;
};

const mergeArrays = <T extends Document>(fromArray: T[], toArray: T[]): T[] => {
	const conflicts: T[] = [];
	fromArray.forEach(doc => {
		const docIndex = toArray.findIndex(({ id }) => doc.id === id);
		if (docIndex === -1) {
			toArray.push(doc);
			return false;
		}
		const currentDoc = toArray[docIndex];
		const currentRevision = currentDoc.revision || 0;
		const docRevision = doc.revision || 0;
		if (currentRevision > docRevision) {
			conflicts.push(doc);
			return false;
		}
		const currentUpdatedAt = new Date(currentDoc.updatedAt || 0);
		const docUpdatedAt = new Date(doc.updatedAt || 0);
		if (currentUpdatedAt > docUpdatedAt) {
			conflicts.push(doc);
			return false;
		}

		toArray[docIndex] = doc;
	});

	return conflicts;
};

export class LocalStorageClient implements ApiClient {
	private static CONTACTS_KEY = 'contacts';
	private static INVOICES_KEY = 'invoices';

	private static instance: LocalStorageClient;

	private constructor(private readonly storage: Storage = localStorage) {}

	doLogin(email: string, password: string) {
		return Promise.resolve(true);
	}

	saveContact(contact: Contact) {
		if (!(contact && contact.id)) {
			return Promise.reject(new Error('Chba id. kontaktu!'));
		}

		const contacts = this.getContactSync();
		const contactIndex = contacts.findIndex(c => c.id === contact.id);

		contact.updatedAt = new Date();
		contact.revision =
			contactIndex > -1 ? (contacts[contactIndex].revision || 0) + 1 : 1;

		if (contactIndex > -1) {
			contacts[contactIndex] = contact;
		} else {
			contacts.push(contact);
		}

		this.storage.setItem(
			LocalStorageClient.CONTACTS_KEY,
			JSON.stringify(contacts)
		);
		return Promise.resolve(contact);
	}

	mergeContacts(contacts: Contact[]): Promise<MergeResult<Contact>> {
		const currentContacts = this.getContactSync();
		const conflicts = mergeArrays(contacts, currentContacts);
		this.storage.setItem(
			LocalStorageClient.CONTACTS_KEY,
			JSON.stringify(currentContacts)
		);
		return Promise.resolve({ items: currentContacts, conflicts });
	}

	fetchContact(id: string): Promise<Contact | null> {
		return this.fetchContacts().then(contacts => {
			const contact = contacts.find(contact => contact.id === id);
			return contact || null;
		});
	}

	fetchContacts(): Promise<Contact[]> {
		return Promise.resolve(this.getContactSync());
	}

	deleteContacts(keys: string[]): Promise<Contact[]> {
		const contacts = this.getContactSync();
		const filtered = contacts.filter(contact => {
			return !keys.includes(contact.id as string);
		});
		this.storage.setItem(
			LocalStorageClient.CONTACTS_KEY,
			JSON.stringify(filtered)
		);
		return Promise.resolve(Object.values(filtered));
	}

	saveInvoice(invoice: Invoice) {
		if (!(invoice && invoice.id)) {
			return Promise.reject(new Error('Chba id. faktry!'));
		}

		const invoices = this.getInvoiceSync();
		const invoiceIndex = invoices.findIndex(c => c.id === invoice.id);

		invoice.updatedAt = new Date();
		invoice.revision =
			invoiceIndex > -1 ? (invoices[invoiceIndex].revision || 0) + 1 : 1;

		if (invoiceIndex > -1) {
			invoices[invoiceIndex] = invoice;
		} else {
			invoices.push(invoice);
		}

		this.storage.setItem(
			LocalStorageClient.INVOICES_KEY,
			JSON.stringify(invoices)
		);
		return Promise.resolve(invoice);
	}

	mergeInvoices(invoices: Invoice[]): Promise<MergeResult<Invoice>> {
		const currentInvoices = this.getInvoiceSync();
		const conflicts = mergeArrays(invoices, currentInvoices);
		this.storage.setItem(
			LocalStorageClient.INVOICES_KEY,
			JSON.stringify(currentInvoices)
		);
		return Promise.resolve({ items: currentInvoices, conflicts });
	}

	fetchInvoice(id: string) {
		return this.fetchInvoices().then(invoices => {
			const invoice = invoices.find(invoice => invoice.id === id);
			return invoice || null;
		});
	}

	fetchInvoices(): Promise<Invoice[]> {
		return Promise.resolve(this.getInvoiceSync());
	}

	deleteInvoices(keys: string[]): Promise<Invoice[]> {
		const invoices = this.getInvoiceSync();
		const filtered = invoices.filter(contact => {
			return !keys.includes(contact.id as string);
		});
		this.storage.setItem(
			LocalStorageClient.INVOICES_KEY,
			JSON.stringify(filtered)
		);
		return Promise.resolve(Object.values(filtered));
	}

	getInvoiceSync(): Invoice[] {
		const invoices = this.storage.getItem(LocalStorageClient.INVOICES_KEY);
		if (!invoices) {
			return [];
		}
		return JSON.parse(invoices);
	}

	getContactSync(): Contact[] {
		const contacts = this.storage.getItem(LocalStorageClient.CONTACTS_KEY);
		if (!contacts) {
			return [];
		}
		return JSON.parse(contacts);
	}

	static getInstance() {
		if (!this.instance) {
			const { CONTACTS_KEY, INVOICES_KEY } = this;
			if (!localStorage.getItem(CONTACTS_KEY)) {
				localStorage.setItem(CONTACTS_KEY, JSON.stringify(mockContacts));
			}
			if (!localStorage.getItem(INVOICES_KEY)) {
				localStorage.setItem(INVOICES_KEY, JSON.stringify(mockInvoices));
			}
			this.instance = new LocalStorageClient(localStorage);
		}
		return this.instance;
	}
}
