import { AnyAction } from 'redux';
import { connect } from 'react-redux';
import { ThunkDispatch } from 'redux-thunk';
import { withRouter } from 'react-router-dom';
import { importArchive, exportArchive } from '../actions';
import { Archive as ArchiveComp } from '../components/Archive';
import { AppState } from '../reducers';

const mapStateToProps = (state: AppState) => {
	return {
		ui: state.ui
	};
};

const mapDispatchToProps = (
	dispatch: ThunkDispatch<AppState, null, AnyAction>
) => {
	return {
		onArchiveExport: () => dispatch(exportArchive()),
		onArchiveImport: (archive: string) => dispatch(importArchive(archive))
	};
};

export const Archive = withRouter(
	connect(
		mapStateToProps,
		mapDispatchToProps
	)(ArchiveComp)
);
