import { types as t, Action } from '../actions';

export const LoginState = {
	LOGGED_OUT: -1,
	LOGIN_IN_PROGRESS: 1,
	LOGGED_IN: 2
};

const initialState = {
	loginState: LoginState.LOGGED_IN
};

export const login = (state: any = initialState, action: Action) => {
	switch (action.type) {
		case t.LOG_IN:
			return {
				loginState: LoginState.LOGIN_IN_PROGRESS
			};
		case t.LOG_IN_SUCCESS:
			return {
				loginState: LoginState.LOGGED_IN
			};
		case t.LOG_IN_FAILURE:
			return {
				loginState: LoginState.LOGGED_OUT
			};
		case t.LOG_OUT:
			return {
				loginState: LoginState.LOGGED_OUT
			};
		default:
			return state;
	}
};
