import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Table, message } from 'antd';
import { MessageType } from 'antd/lib/message';
import { ColumnProps } from 'antd/lib/table/interface';
import { History } from 'history';
import { Invoice } from '../../classes/Invoice';
import { dateFormat, currencyFormat } from '../../utils/formatters';
import { InvoiceUIState } from '../../reducers/invoiceUI';
import './style.css';

export interface InvoiceListProps {
	invoices: Invoice[];
	history: History;
	onDelete: Function;
	onCreate: Function;
	ui: InvoiceUIState;
}

export class InvoiceList extends Component<InvoiceListProps> {
	private hideSavingProgressMessage: MessageType | null = null;

	state = {
		selectedRowKeys: [] // Check here to configure the default column
	};

	componentWillUpdate(nextProps: InvoiceListProps) {
		const { isSaving: wasSaving } = this.props.ui;
		const { isSaving } = nextProps.ui;
		if (!isSaving && wasSaving) {
			this.hideMessage();
			message.success('Zmeny boli uloen', 1);
			this.setState({ selectedRowKeys: [] });
		} else if (isSaving) {
			this.hideMessage();
			this.hideSavingProgressMessage = message.loading(
				'Prebieha ukladanie...',
				0
			);
		}
	}

	componentWillUnmount() {
		this.hideMessage();
	}

	hideMessage() {
		if (this.hideSavingProgressMessage) {
			this.hideSavingProgressMessage(); // hide message
		}
		this.hideSavingProgressMessage = null;
	}

	onSelectChange = (selectedRowKeys: any) => {
		this.setState({ selectedRowKeys });
	};

	handleDelete = () => {
		const { selectedRowKeys } = this.state;
		this.props.onDelete(selectedRowKeys);
	};

	handleCreateNewInvoice = () => {
		this.props.onCreate(this.props.history);
	};

	render() {
		const { invoices, ui } = this.props;
		const { selectedRowKeys } = this.state;
		const rowSelection = {
			selectedRowKeys,
			onChange: this.onSelectChange
		};
		const hasSelected = selectedRowKeys.length > 0;

		return (
			<div className="fz-list-page">
				<div className="fz-btn-group">
					<Button
						type="primary"
						icon="form"
						size="large"
						onClick={this.handleCreateNewInvoice}
					>
						Vystavi
					</Button>
					<Button
						icon="delete"
						size="large"
						disabled={!hasSelected}
						onClick={this.handleDelete}
					>
						Vymaza
					</Button>
				</div>
				<div className="fz-table-wrapper">
					<Table
						className="fz-table fz-invoice-table"
						rowKey="id"
						dataSource={invoices}
						columns={columns}
						rowSelection={rowSelection}
						loading={ui.isFetching}
						// scroll={{ x: 'max-content' }}
						// onRow={(record, rowIndex) => {
						// 	return {
						// 		onClick: () => history.push(`/invoices/${record.id}`)
						// 	};
						// }}
					/>
				</div>
			</div>
		);
	}
}

const columns: ColumnProps<any>[] = [
	{
		title: 'Id. dokladu',
		dataIndex: 'id',
		key: 'id',
		render: function invoiceLink(text: any) {
			return <Link to={`/invoices/${text}`}>{text}</Link>;
		}
	},
	{
		title: 'Popis',
		className: 'fz-invoice-table__item fz-invoice-table__item--wide',
		dataIndex: 'description',
		key: 'description'
	},
	{
		title: 'Odberate',
		className: 'fz-invoice-table__item fz-invoice-table__item--wide',
		dataIndex: 'recipient.entity',
		key: 'recipient'
	},
	{
		title: 'Suma',
		dataIndex: 'netPrice',
		key: 'netPrice',
		render: (text: any) => currencyFormat.format(text)
	},
	{
		title: 'Dtum vystavenia',
		dataIndex: 'issueDate',
		key: 'issueDate',
		render: (text: any) => dateFormat.format(new Date(text))
	},
	{
		title: 'Splatnos',
		dataIndex: 'dueDate',
		key: 'dueDate',
		render: (text: any) => dateFormat.format(new Date(text))
	}
];
