import React, { Component } from 'react';
import { Form, Row, Col, Input, Select } from 'antd';
import { WrappedFormUtils } from 'antd/lib/form/Form';
import { isValidIBANNumber } from '../../utils/iban';

const columnLayout = {
	xs: 24,
	sm: 16,
	lg: 12
};

export interface BankProps extends WrappedFormUtils {
	isRequired: boolean;
	bankList: [string, string, string][];
	fieldPrefix?: string;
}

export class Bank extends Component<BankProps> {
	// componentDidMount() {
	// 	this.props.validateFields();
	// }

	validateIBAN = () => {
		const { getFieldsValue, fieldPrefix = '' } = this.props;
		const values = getFieldsValue();
		const iban = values[`${fieldPrefix}iban`];

		if (!iban) {
			return null;
		}

		const result = isValidIBANNumber(iban);
		if (result === false) {
			return 'Pravdepodobne nesprvny IBAN.';
		}
		if (result !== 1) {
			return 'Chyba kontrolnho stu. Pravdepodobne nesprvny IBAN.';
		}

		const swift = values[`${fieldPrefix}swift`];
		const bankCode = values[`${fieldPrefix}bankCode`];
		const bankAccount = values[`${fieldPrefix}bankAccount`];

		if (swift) {
			if (iban.substring(0, 2) != swift.substring(4, 6)) {
				return 'Kd krajiny (SWIFT) sa nezhoduje.';
			}
		}
		if (bankCode) {
			if (iban.substring(4, 4 + bankCode.length) != bankCode) {
				return 'Kd banky sa nezhoduje.';
			}
		}
		if (bankAccount) {
			if (!iban.endsWith(bankAccount.toUpperCase().replace(/[^A-Z0-9]/g, ''))) {
				return 'slo tu sa nezhoduje.';
			}
		}
	};

	render() {
		const {
			isRequired,
			bankList,
			fieldPrefix = '',
			getFieldDecorator
		} = this.props;

		let ibanHelp = this.validateIBAN();

		return (
			<Row className="fz-contact-row">
				<Col {...columnLayout}>
					<Form.Item label="Zoznam bnk">
						{getFieldDecorator(`${fieldPrefix}bankIndex`, {
							preserve: true
						})(
							<Select>
								{bankList.map(([name, code], index) => (
									<Select.Option key={code} value={index}>
										{code} - {name}
									</Select.Option>
								))}
							</Select>
						)}
					</Form.Item>
					<Form.Item label="Nzov banky">
						{getFieldDecorator(`${fieldPrefix}bankName`, {
							preserve: true,
							rules: [
								{
									required: isRequired,
									message: 'Uvete nzov banky!'
								}
							]
						})(<Input />)}
					</Form.Item>
					<Form.Item label="Kd banky">
						{getFieldDecorator(`${fieldPrefix}bankCode`, {
							preserve: true,
							rules: [
								{
									required: isRequired,
									message: 'Uvete kd banky!'
								}
							]
						})(<Input />)}
					</Form.Item>
					<Form.Item label="SWIFT">
						{getFieldDecorator(`${fieldPrefix}swift`, {
							preserve: true,
							rules: [
								{
									required: isRequired,
									message: 'Uvete SWIFT!'
								},
								{
									min: 8,
									message: 'Minimlne 8 znakov!'
								}
							]
						})(<Input />)}
					</Form.Item>
					<Form.Item
						hasFeedback
						label="IBAN"
						help={ibanHelp}
						validateStatus={ibanHelp ? 'warning' : ''}
					>
						{getFieldDecorator(`${fieldPrefix}iban`, {
							validateTrigger: 'onBlur',
							preserve: true,
							rules: [
								{
									required: isRequired,
									message: 'Uvete IBAN!'
								}
							]
						})(<Input />)}
					</Form.Item>
					<Form.Item label="slo tu">
						{getFieldDecorator(`${fieldPrefix}bankAccount`, {
							preserve: true,
							rules: [
								{
									required: isRequired,
									message: 'Uvete slo tu!'
								}
							]
						})(<Input />)}
					</Form.Item>
				</Col>
			</Row>
		);
	}
}
