import { types as t, Action } from '../actions';

export type UIState = {
	lastError: Error | null;
	isFetching: boolean;
	isSaving: boolean;
};

const initialState: UIState = {
	lastError: null,
	isFetching: false,
	isSaving: false
};

export const ui = (state: UIState = initialState, action: Action): UIState => {
	const { type } = action;
	switch (type) {
		case t.ARCHIVE_IMPORT:
			return { ...state, isSaving: true, lastError: null };
		case t.ARCHIVE_IMPORT_SUCCESS:
			return { ...state, isSaving: false };
		case t.ARCHIVE_IMPORT_FAILURE:
			return { ...state, isSaving: false, lastError: action.payload };
		case t.ARCHIVE_EXPORT:
			return { ...state, isFetching: true, lastError: null };
		case t.ARCHIVE_EXPORT_SUCCESS:
			return { ...state, isFetching: false };
		case t.ARCHIVE_EXPORT_FAILURE:
			return { ...state, isFetching: false, lastError: action.payload };
		default:
		/* noop */
	}

	switch (true) {
		case /DELETE$/.test(type):
			return { ...state, isSaving: true, lastError: null };
		case /DELETE_SUCCESS$/.test(type):
			return { ...state, isSaving: false };
		case /DELETE_FAILURE$/.test(type):
			return { ...state, isSaving: false, lastError: action.payload };
		case /FETCH$/.test(type):
			return { ...state, isFetching: true, lastError: null };
		case /FETCH_SUCCESS$/.test(type):
			return { ...state, isFetching: false };
		case /FETCH_FAILURE$/.test(type):
			return { ...state, isFetching: false, lastError: action.payload };
		default:
			return state;
	}
};
