import React, { Component } from 'react';
import { History } from 'history';
import { PageHeader, Icon, BackTop, Modal, message } from 'antd';
import { WrappedFormUtils } from 'antd/lib/form/Form';
import LoadingOverlay from 'react-loading-overlay';
import { EnhancedInvoiceForm as InvoiceForm } from './InvoiceForm';
import { EnhancedInvoiceIdForm } from './InvoiceIdForm';
import { Spinner } from '../Spinner';
import { Contact } from '../../classes/Contact';
import { Invoice } from '../../classes/Invoice';
import { InvoiceMapper } from '../../classes/InvoiceMapper';
import { InvoiceItem } from '../../classes/InvoiceItem';
import { InvoiceUIState } from '../../reducers/invoiceUI';
import { messageBroadcast } from '../../utils/broadcast';
import constantSymbols from './constantSymbols';
import './style.css';

export interface EditInvoiceProps extends InvoiceUIState {
	invoiceId: string;
	fields: any;
	items: InvoiceItem[];
	netPrice: number;
	grossPrice: number;
	discountPrice: number;
	isFetching: boolean;
	isSaving: boolean;
	history: History;
	location: any;
	contacts: Contact[];
	fetchForm: (id: string) => void;
	onSave: (values: any, itemKeys: number[]) => Promise<Invoice>;
	onFieldsChange: (changedFields: any, allFields: any) => void;
	onInvoiceChangeId: (
		newId: string,
		values: any,
		itemKeys: number[],
		routerHistory: History
	) => void;
	onInvoiceCheckExists: (id: string) => Promise<boolean>;
	onItemAdd: (invoiceId: string) => void;
	onItemRemove: (invoiceId: string, k: number) => void;
	onEntityCreate: (
		values: any,
		itemKeys: number[],
		isOwn: boolean,
		routerHistory: History
	) => void;
}

export interface EditInvoiceState {
	isChangeIdModalVisible: boolean;
}

export class EditInvoice extends Component<EditInvoiceProps, any> {
	private formRef: any;
	private changeIdFormRef: any;

	state: EditInvoiceState = {
		isChangeIdModalVisible: false
	};

	componentWillMount() {
		this.props.fetchForm(this.props.invoiceId);
	}

	componentWillReceiveProps(nextProps: EditInvoiceProps) {
		if (nextProps.invoiceId !== this.props.invoiceId) {
			this.props.fetchForm(nextProps.invoiceId);
		}
	}

	handleSubmit = (e: React.FormEvent, form: WrappedFormUtils) => {
		e.preventDefault();
		const values = form.getFieldsValue();
		const hide = message.loading('Ukladm faktru...', 0);
		this.props
			.onSave(values, this.props.itemKeys)
			.then(hide)
			.catch(error => {
				hide();
				message.error(error.message);
			});
	};

	handleItemAdd = () => {
		this.props.onItemAdd(this.props.invoiceId);
	};

	handleItemRemove = (k: number) => {
		this.props.onItemRemove(this.props.invoiceId, k);
	};

	handleFieldsChange = (changedFields: any, allFields: any) => {
		this.props.onFieldsChange(changedFields, allFields);
	};

	handleEntityCreate = (isOwn: boolean) => {
		const values = this.formRef.props.form.getFieldsValue();
		this.props.onEntityCreate(
			values,
			this.props.itemKeys,
			isOwn,
			this.props.history
		);
	};

	showChangeIdModal = () => {
		this.setState({
			isChangeIdModalVisible: true
		});
	};

	handleChangeId = () => {
		const { props } = this.changeIdFormRef;
		props.form.validateFields((err: Error, values: any) => {
			if (!err) {
				const { itemKeys, history } = this.props;
				const invoiceValues = this.formRef.props.form.getFieldsValue();
				this.props.onInvoiceChangeId(
					values.invoiceId,
					invoiceValues,
					itemKeys,
					history
				);
				this.setState({
					isChangeIdModalVisible: false
				});
			}
		});
	};

	handleCancelChangeId = () => {
		const { props } = this.changeIdFormRef;
		props.form.setFieldsValue({ invoiceId: this.props.invoiceId });
		this.setState({
			isChangeIdModalVisible: false
		});
	};

	handleCheckInvoiceExists = (value: any) => {
		return this.props.onInvoiceCheckExists(value);
	};

	handleBackButton = () => {
		this.props.history.goBack();
	};

	makePdf = () => {
		const { props } = this.formRef;
		props.form.validateFieldsAndScroll((err: Error, values: any) => {
			if (!err) {
				const hideSaveMsg = message.loading('Ukladm faktru...', 0);
				const { itemKeys } = this.props;
				this.props.onSave(values, itemKeys).then(
					invoice => {
						hideSaveMsg();
						const hideLoadMsg = message.loading('Generujem pdf...', 0);
						const mapper = new InvoiceMapper();
						const invoiceData = mapper.toPlainObject(invoice);

						// Check for IE 9-11 only
						// documentMode is an IE-only property
						const msie = (document as any).documentMode;
						if (msie) {
							// Workaround: for IEs we have to generate pdf in iframe,
							// because pdfmaker doesn't work with polyfills
							if (document.getElementById('pdfmaker')) {
								messageBroadcast({
									command: 'makeInvoicePdf',
									data: invoiceData
								});
								hideLoadMsg();
							} else {
								const ifrm = document.createElement('iframe');
								ifrm.setAttribute('id', 'pdfmaker');
								ifrm.src = `${window.location.origin}/pdf.html`;
								// ifrm.src = 'http://localhost:5000/pdf.html';
								ifrm.onload = function() {
									messageBroadcast({
										command: 'makeInvoicePdf',
										data: invoiceData
									});
									hideLoadMsg();
								};
								document.body.appendChild(ifrm);
							}
						} else {
							import('../../utils/PdfMaker')
								.then(({ PdfMaker }) => {
									hideLoadMsg();
									const pdfMaker = new PdfMaker();
									return pdfMaker.makeInvoicePdf(invoiceData);
								})
								.catch(error => {
									hideLoadMsg();
									message.error(`Natanie kninice zlyhalo: ${error.message}`);
								});
						}
					},
					error => {
						hideSaveMsg();
						message.error(error.message);
					}
				);
			}
		});
	};

	render() {
		const {
			invoiceId,
			fields,
			items,
			netPrice,
			grossPrice,
			discountPrice,
			contacts,
			isFetching,
			isSaving
		} = this.props;

		return (
			<div>
				<Modal
					title="Zmena identifiktora faktry"
					visible={this.state.isChangeIdModalVisible}
					onOk={this.handleChangeId}
					onCancel={this.handleCancelChangeId}
				>
					<EnhancedInvoiceIdForm
						wrappedComponentRef={(inst: any) => {
							this.changeIdFormRef = inst;
						}}
						invoiceId={invoiceId}
						onInvoiceCheckExists={this.handleCheckInvoiceExists}
					/>
				</Modal>
				<LoadingOverlay
					classNamePrefix="fz-loading-overlay_"
					active={isFetching}
					spinner={<Spinner />}
					text="Nahrvam faktru..."
					styles={{
						// wrapper: {},
						overlay: {},
						content: {},
						spinner: {}
					}}
				>
					<PageHeader
						onBack={this.handleBackButton}
						title={
							<span>
								<span>Faktra&nbsp;{invoiceId}&nbsp;&nbsp;</span>
								<Icon
									type="edit"
									style={{ color: '#40a9ff' }}
									onClick={this.showChangeIdModal}
								/>
							</span>
						}
						subTitle=""
					/>
					<InvoiceForm
						wrappedComponentRef={(inst: any) => {
							this.formRef = inst;
						}}
						fields={fields}
						items={items}
						netPrice={netPrice}
						grossPrice={grossPrice}
						discountPrice={discountPrice}
						contacts={contacts}
						constantSymbols={constantSymbols}
						isSaving={isSaving}
						onSubmit={this.handleSubmit}
						onPdfClick={this.makePdf}
						onItemAdd={this.handleItemAdd}
						onItemRemove={this.handleItemRemove}
						onEntityCreate={this.handleEntityCreate}
						onFieldsChange={this.handleFieldsChange}
					/>
					<BackTop className="fz-back-top">
						<div className="fz-back-top-inner">
							<Icon type="up" />
						</div>
					</BackTop>
					{/* <pre className="language-bash">{JSON.stringify(fields, null, 2)}</pre> */}
				</LoadingOverlay>
			</div>
		);
	}
}
